<style>
	.news_component_wrap{
		margin-bottom: 20px;
		border-top: 1px solid #f0f0f0;
	}


	/* 一个图片固有样式 */
	.newscontents_wrap{
		padding-top: 20px;
	}
	.newscontents_wrap .newscontents_left_wrap{
		float: left;
	}
	.newscontents_wrap .newscontents_left_wrap a.newscontents_left_href{
		cursor: pointer;
	}
	.newscontents_wrap .newscontents_left_wrap a.newscontents_left_href img.contents_suolueimg{
		width: 179px;
	}
	.newscontents_wrap .newscontents_right_wrap{
		float: left;
		margin-top: 25px;
		margin-left: 20px;
		width: 330px;
	}
	.newscontents_wrap .newscontents_right_wrap .contents_title_wrap{
		overflow: hidden; /* 隐藏多余部分 */
		text-overflow: ellipsis; /* 文字隐藏之后加省略号 */
		white-space: nowrap;  /* 文本不换行 */
	}


	.news_component_wrap .contents_title_wrap{
		clear: both;
		margin-bottom: 10px;
	}
	.news_component_wrap .contents_title_wrap span.contents_title{
		font-size: 18px;
		font-family: 微软雅黑;
		font-weight: bold;
		cursor: pointer;
	}
	.news_component_wrap .contents_right_web_href{
		text-decoration: none;
	}
	.news_component_wrap .contents_right_web_href .contents_right_time_web{
		cursor: pointer;
	}
	.contents_right_hots{
		padding: 0 2px;
		margin-left: 5px;
		line-height: 12px;
		color: #f13f40!important;
		border: 1px solid #efb9b9;
		font-size: 12px!important;
		border-radius: 3px;
		-webkit-border-radius: 3px;
	}
	

	/* 鼠标滑动 */
	.news_component_wrap .contents_title_wrap span.contents_title:hover{
		color: #07f;
		text-decoration: underline;
	}

	.news_component_wrap .contents_pic_wrap li.contents_pic{
		float: left;
		margin: 0 2px;
		cursor: pointer;
	}
	/* 图片共同样式 */
	.contents_left_contents_wrap .contents_pic_wrap img.contents_img{
		width: 179px;
		/* height: 119px; */
	}
	.news_component_wrap .contents_bottom{
		position: relative;
		margin-top: 10px;
	}
	.news_component_wrap .contents_bottom span.contents_right_time_web,
	.news_component_wrap .contents_bottom span.contents_bottom_time,
	.news_component_wrap .contents_bottom span.contents_bottom_hot{
		color: rgba(0,0,0,.4);
		font-size: 13px;
		cursor: default;
	}


	/* 关闭按钮 */
	.close_text{
		position: absolute;
		right: -30px;
		bottom: 32px;
	}
	.close_text_main{
		width: 80px;
		height: 30px;
		text-align: center;
		background-color: #fff;
		border: 1px solid #e3e3e3;
		box-shadow: 1px 2px 1px 0 rgba(0,0,0,0.072);
		-webkit-box-shadow: 1px 2px 1px 0 rgba(0,0,0,0.072);
		border-radius: 1px;
		-webkit-border-radius: 1px;
	}
	.close_text_main>span{
		line-height: 30px;
		font-size: 13px;
		color: #666;
	}
	.close_text_bottom{
		position: absolute;
		left: 35px;
		border: 7px dashed transparent;
	    border-top: 7px solid #e3e3e3;
	    width: 0px;
	}
	.close_text_bottom>em{
		position: absolute;
		top: -8px;
		left: -7px;
		border: 7px dashed transparent;
	    border-top: 7px solid #FFF;
	    width: 0px;
	}
	.close_icon{
		display: block;
		position: absolute;
		right: 0;
		top: 0px;
		width: 19px;
		height: 15px;
		background-image: url(./../../../../assets/close_icon.png);
		background-position: 0 -19px;
		background-repeat: no-repeat;
		cursor: pointer;
	}

	/* 鼠标滑动 */
	.close_icon:hover{
		background-position: 0 0;
	}
</style>


<template>
	<div 
		:class="news.newsclassname"
		class='news_component_wrap'
		@mouseenter="iconNow=1"
		@mouseleave="iconNow=0"
		:style="{borderTop: '1px solid rgba(240,240,240,'+ backgroundopacity +')'}"
		v-show="newsNow===1">
		<div class="newscontents_left_wrap">
			<a 
				class="newscontents_left_href" 
				:title="news.newsTitle">
				<img 
					class="contents_suolueimg" 
					:src="news.imgsuolueurl">
			</a>
		</div>
		<div class="newscontents_right_wrap">
			<div class="contents_title_wrap">
				<span
					class="contents_title"
					:title="news.newsTitle">
					{{ news.newsTitle }}
				</span>
			</div>
			<ul class="contents_pic_wrap">
				<li 
					class="contents_pic"
					v-for="list in news.imgurl">
					<a :title="news.newsTitle">
						<img
							class="contents_img"
							:src="list.imgsrc">
					</a>
				</li>
				<div class="div_clear"></div>
			</ul>
			<div class="contents_bottom">
				<a 
					class="contents_right_web_href"
					:href="news.comeFromWebHref">
					<span class="contents_right_time_web">
						{{ news.comeFromWeb }}
					</span>
				</a>
				<span class="contents_bottom_time">
					{{ news.time }}
				</span>
				<span 
					class="contents_bottom_hot" 
					:class="news.hotclassname">
					{{ news.hot }}
				</span>
				<div 
					class="close_text"
					v-show="textNow===1">
					<div class="close_text_main">
						<span>不感兴趣</span>
					</div>
					<div class="close_text_bottom">
						<em></em>
					</div>
				</div>
				<div 
					class="close_icon"
					v-show="iconNow===1"
					@mouseenter="textNow=1"
					@mouseleave="textNow=0"
					@click="newsNow=0"></div>
			</div>
		</div>
		<div class="div_clear"></div>
	</div>
</template>


<script>
	export default{
		props: ['news','backgroundopacity'],
		data(){
			return{
				newsNow: 1,
				iconNow: 0,
				textNow: 0
			}
		}
	}
</script>


