<style>
	.main_contents{
		position: relative;
		margin: 0 auto;
		width: 900px;
		height: 416px;
		min-height: 416px;
		border: 1px solid rgba(235,235,235,1);
		background-color: rgba(255,255,255,1);
		overflow: hidden;
	}
	.main_contents ul.main_contents_title_wrap{
		clear: both;
		height: 40px;
		border-bottom: 1px solid rgba(235,235,235,1);
	}
	.main_contents ul.main_contents_title_wrap li.main_contents_title{
		float: left;
		position: relative;
		padding: 0 20px;
		line-height: 40px;
		cursor: pointer;
	}
	.main_contents ul.main_contents_title_wrap li.main_contents_title div.main_contents_icon,
	.main_contents ul.main_contents_title_wrap li.main_contents_title div.main_contents_titlename_wrapper{
		float: left;
	}
	/* titlename最外层 */
	.main_contents ul.main_contents_title_wrap li.main_contents_title div.main_contents_titlename_wrapper{
	}
	/* 鼠标滑动显示的背景颜色div */
	.main_contents ul.main_contents_title_wrap li.main_contents_title div.main_contents_backgroundcolor{
		z-index: 1;
		position: absolute;
		top: 0;
		left: 0;
		width: 68px;
		height: 41px;
		background-color: rgba(179,182,187,1);
	}
	/* 点击显示的背景颜色div */
	.main_contents ul.main_contents_title_wrap li.main_contents_title .main_contents_clickbackgroundcolor{
		z-index: 5;
		position: absolute;
		top: 0;
		left: 0;
		width: 68px;
		height: 41px;
		background-color: rgba(154,157,162,1);
	}
	.main_contents ul.main_contents_title_wrap li.main_contents_title div.main_contents_first_backgroundcolor{
		width: 116px;
	}
	/* titlename外层div */
	.main_contents ul.main_contents_title_wrap li.main_contents_title div.main_contents_titlename_wrap{
		position: relative;
		z-index: 100;
	}
	/* 字体样式 */
	.main_contents ul.main_contents_title_wrap li.main_contents_title span.main_contents_titlename{
		color: #333;
		font-size:14px;
		font-weight: normal;
	}

	.main_contents ul.main_contents_title_wrap .icon_set_up_wrap{
		float: right;
		padding: 0;
		margin-right: 20px;
		cursor: auto;
	}
	.main_contents ul.main_contents_title_wrap li.main_contents_title .icon{
		display: block;
		position: relative;
		z-index: 10000;
		margin-top: 12px;
		width: 20px;
		height: 20px;
		background-image: url(./../../../assets/icon.png);
		background-position: -15px 0px;
		background-repeat: no-repeat;
	}
	.icon_set_up{
		display: block;
		margin-top: 12px;
		width: 20px;
		height: 20px;
		background-image: url(./../../../assets/icon.png);
		background-position: -121px -49px;
		background-repeat: no-repeat;
		cursor: pointer;
	}

	/* 鼠标滑动 */
	.icon_set_up:hover{
		background-position: -121px -73px;
	}
	.main_contents ul.main_contents_title_wrap li.main_contents_title:hover span.main_contents_titlename{
		font-weight: normal;
		color: #fff;
	}
	.main_contents ul.main_contents_title_wrap >li.main_contents_title:hover .icon{
		background-position: -144px 0;
	}

	/* 选择时候的样式 */
	.main_contents ul.main_contents_title_wrap .contents_title_click{
		/* background-color: rgba(154,157,162,1)!important; */
		font-weight: bold;
	}
	/* .main_contents ul.main_contents_title_wrap .contents_title_click .main_contents_backgroundcolor{
		background-color: rgba(154,157,162,1)!important;
	} */
	.main_contents ul.main_contents_title_wrap .contents_title_click span{
		font-weight: bold!important;
		color: #fff;
	}
	.main_contents ul.main_contents_title_wrap .contents_title_click .icon{
		background-position: -144px 0!important;
	}

	
	/* 鼠标滑动添加样式 */
	.main_contents_wheel{
		height: auto;
		overflow: visible;
	}
</style>


<template>
	<div 
		class="main_contents"
		:class="(mousewheeldown ? 'main_contents_wheel' : '')"
		:style="{backgroundColor : 'rgba(255,255,255,'+ backgroundopacity +')' , 
				 border : '1px solid rgba(235,235,235,'+ (backgroundopacity===0 ? 0.2 : backgroundopacity) +')'}">
		<ul 
			class="main_contents_title_wrap"
			:style="{borderBottom : '1px solid rgba(235,235,235,'+ (backgroundopacity===0 ? 0.2 : backgroundopacity) +')'}">
			<li 
				class="main_contents_title" 
				v-for="(list,index) in mainContentsTitle"
				:class="(index===listNow?'contents_title_click':'')"
				@click="listNow=index"
				@mouseenter="hoverlist=index"
				@mouseleave="hoverlist=''">
				<!-- 小图标 -->
				<div 
					class="main_contents_icon" 
					:class="list.className"></div>
				<div class="main_contents_titlename_wrapper">
					<!-- 鼠标滑动显示颜色的div -->
					<div 
						v-show="hoverlist===index"
						class="main_contents_backgroundcolor"
						:class="list.backgroundclassname"
						:style="{backgroundColor : 'rgba(179,182,187,'+ (backgroundopacity===0 ? 0.4 : backgroundopacity) +')'}"></div>
					<!-- 鼠标点击显示颜色的div -->
					<div
						v-show="listNow===index"
						class="main_contents_clickbackgroundcolor"
						:class="list.backgroundclassname"
						:style="{backgroundColor : 'rgba(154,157,162,'+ (backgroundopacity===0 ? 0.4 : backgroundopacity) +')'}"></div>
					<!-- 字体的div -->
					<div class="main_contents_titlename_wrap">
						<span class="main_contents_titlename">
							{{ list.titleName }}
						</span>
					</div>
					<div class="div_clear"></div>
				</div>
			</li>
			<li 
				class="icon_set_up_wrap"
				@click="setupNow===1 ? setupNow=0 : setupNow=1">
				<div class="icon_set_up">
				</div>
			</li>
		</ul>
		<threemaincontents
			:listnumber="listNow"
			:changebackgroundopacity="backgroundopacity"></threemaincontents>
		<setup
			v-show="setupNow===1"></setup>
	</div>
</template>


<script>
	import threemaincontents from './threemaincontents.vue'
	import setup from './setup.vue'

	export default{
		props: ['mousewheeldown','backgroundopacity'],
		data(){
			return {
				listNow: 1,
				hoverlist: '',
				mainContentsTitle: [
					{
						className: 'icon',
						titleName: '我的关注',
						backgroundclassname: 'main_contents_first_backgroundcolor'
					},
					{
						className: '',
						titleName: '推荐'
					},
					{
						className: '',
						titleName: '导航'
					}
				],
				setupNow: '0'
			}
		},
		components: {
			threemaincontents,
			setup
		},
		computed: {
			/*backgroundopacity: function(){
				if(backgroundopacity<=0){
					console.log(1)
				}
			}*/
		},
		watch: {
			
		}
		/*med:{
			contentOnMouseWhee:function(){
				this.mainContentLoadMore()
			},
			mainContentLoadMore:function(){

			}
		}*/
	}
</script>


