<style>
  .topnav_left_div{
    float: left;
    margin-left: 10px;
    line-height: 32px;
  }
  .topnav_left_div span{
    color: #555;
    font-size: 12px;
    text-decoration: none;
    cursor: pointer;
  }
  .topnav_left_div .topnav_text{
    color: #f03b32!important;
    font-weight: bold;
  }
  .topnav_left_div>img{
    position: relative;
    top: 5px;
  }
</style>


<template>
	<div
		class="topnav_left_div">
		<span>
			{{ location }}
		</span>
		<img
			:src="weatherImgUrl">
		<span>
			{{ temperature }}℃
		</span>
		<span
			class="topnav_text">
			{{ airquality.degree }}
		</span>
		<span>
			{{ airquality.degreecount }}
		</span>
		<span>|</span>
	</div>
</template>

<script>
	export default{
		data() {
			return {
				location: '北京',
				weatherImgUrl: require('@/assets/sun.png'),
				temperature: '20',
				airquality: {
					degree: '严重',
					degreecount: '500'
				}
			}
		}
	}
</script>


