<style>
	.topdropmenu_wrap{
		position: absolute;
		top: 32px;
		left: 50%;
		margin-left: -25px;
		width: 70px;
		border: 1px solid #fff;
		box-shadow: 1px 1px 5px #d1d1d1;
		-webkit-box-shadow: 1px 1px 5px #d1d1d1;
	}
	.topdropmenu_list{
		display: block;
		padding-left: 11px;
		height: 25px;
		line-height: 22px;
		cursor: pointer;
	}
	.topdropmenu_list>a{
		text-align: center;
		color: #000;
		font-size: 12px;
	}

	
	/* 鼠标滑动样式 */
	.topdropmenu_wrap .topdropmenu_list:hover{
		background-color: #38F;
	}
	.topdropmenu_wrap .topdropmenu_list:hover>a{
		color: #FFF;
	}


	.topdropmenu_top{
		position: absolute;
	    z-index: 2;
	    top: -11px;
	    left: 28px;
	    display: inline-block;
	    width: 0;
	    height: 0;
	    line-height: 0;
	    border: 5px dashed transparent;
	    border-bottom: 5px solid #e3e3e3;
	    font-size: 0;
	}
	.topdropmenu_top em{
		position: absolute;
	    top: -4px;
	    left: -5px;
	    display: block;
	    width: 0;
	    height: 0;
	    line-height: 0;
	    font-size: 0;
	    color: #666;
	    border-top: 5px dashed transparent;
	    border-left: 5px dashed transparent;
	    border-right: 5px dashed transparent;
	    border-bottom: 5px solid #fff;
	}


	/* css动画 */
	/* 向下滑动显示，向上滑动消失 */
	.dropshow-enter-active{
		transition: all .5s ease-out;
	}
	.dropshow-leave-active{
		transition: all .5s ease-out;
	}
	.dropshow-enter,
	.dropshow-leave-active{
		transform: translateY(-300px);
		opacity: 0;
	}
</style>


<template>
	<ul class="topdropmenu_wrap">
		<div class="topdropmenu_top">
			<em></em>
		</div>
		<li 
			v-for="(list,index) in message"
			class="topdropmenu_list"
			@click.stop="clicktopmenulist(index)">
			<a>{{ list.submenu }}</a>
		</li>
	</ul>
</template>


<script>
	export default{
		props: ['message'],
		data(){
			return{
				clicktopdropmenu: ''
			}
		},
		methods: {
			clicktopmenulist: function(index){
				this.clicktopdropmenu=(index===0);
				this.$emit('clicktopdropmenu',this.clicktopdropmenu);
			}
		}
	}
</script>


