<style>
  /* 中间部分 */
  .main_pic_wrap{
    height: 300px;
    margin-top: 60px;
  }
  .main_pic{
    width: 100%;
    text-align: center;
  }
  .main_pic>img{
    width: 270px;
    height: 129px;
    cursor: pointer;
  }
  .main_input{
    position: relative;
    margin: 22px auto 0;
      width: 641px;
      text-align: left;
      z-index: 100;
  }
  .main_input>form{
    height: 36px;
  }
  .main_bottom_wrap{
    display: block;
    margin: 20px auto 0;
    width: 72px;
    height: 65px;
    cursor: pointer;
  }
  .main_bottom_wrap span.main_bottom_text{
    color: #999;
    font-size: 12px;
  }
  .main_bottom_icon{
    margin-left: 18px;
    margin-top: 8px;
    display: block;
    width: 34px;
    height: 18px;
    background-image: url(./../../assets/icon.png);
    background-position: -1px -36px;
    background-repeat: no-repeat;
  }

  /* 鼠标往下滚动时会隐藏下面的字体和下箭头 */
  .wheelscrolldown_bottom{
    display: none;
  }
</style>


<template>
	<!-- 中间部分 -->
  <div>
    <div 
      class="main_pic_wrap">
      <div 
        class="main_pic">
        <img
          :src="changecentersomestyle ? baidulogoimgwhite : baidulogoimg"
          :title="baidulogotitle">
      </div>
      <div class="main_input">
        <searchpart
          :changebtnbackgroundimg="changecentersomestyle"></searchpart>
      </div>
    </div>
    <maincontents
      :mousewheeldown="mousedirection"
      :backgroundopacity="changebackgroundopacity"></maincontents>
    <div 
      class="main_bottom_wrap"
      :class="{wheelscrolldown_bottom : mousedirection}"
      @click="mousedirection=true">
      <div class="main_bottom">
        <span class="main_bottom_text">
          {{ scrollname }}
        </span>
      </div>
      <div class="main_bottom_icon"></div>
    </div>
  </div>
</template>


<script>
  import searchpart from './searchmain/searchpart.vue'
	import maincontents from './maininformation/maincontents.vue'

	export default{
    props: ['changecentersomestyle','mousedirection','changebackgroundopacity'],
		data(){
			return {
       	 	scrollname: '滚动查看更多',
          baidulogoimg: require('./../../assets/baidu_logo.png'),
          baidulogoimgwhite: require('./../../assets/baidu_logo_white.png'),
          baidulogotitle: '点一下了解更多'
			}
		},
    components: {
      searchpart,
			maincontents
    },
    methods: {
    }
	}
</script>


