<style>
	.camera_div_wrap{
		position: absolute;
		top: 0;
		left: 0;
		display: block;
		box-shadow: none;
		border: 1px solid #3385ff;
		background-color: #fff;
	}
	.camera_div_main{
		padding: 23px 20px 27px;
	}
	.camera_div_main_top{
		text-align: center;
	    height: 75px;
	    padding-top: 25px;
	    margin-bottom: 16px;
	    background-color: #fafafa;
	}
	.camera_div_main_top>span{
		display: block;
		font-size: 16px;
		color: #999;
	}
	.camera_div_main_top>i{
		display: inline-block;
		margin-top: 6px;
	    width: 28px;
	    height: 28px;
	    background-position: -70px -2px;
	    background-image: url(./../../../assets/mine3.png);
	    background-repeat: no-repeat;
	}
	.camera_div_main_bottom{
		position: relative;
	    width: 158px;
	    height: 43px;
	    font-size: 16px;
	    border: 1px solid #cacbcc;
	    line-height: 43px;
	    margin: 0 auto;
	    color: #333;
	    text-align: center;
	}
	.camera_div_main_bottom>input{
		position: absolute;
	    margin-left: -18px;
	    width: 100%;
	    height: 100%;
	    font-size: 0;
	    outline: 0;
	    opacity: 0;
	    z-index: 1;
	    cursor: pointer;
	}
	.camera_div_main_bottom>i{
		display: inline-block;
		position: relative;
	    top: 2px;
	    width: 18px;
	    height: 18px;
	    margin-right: 8px;
	    background-position: -144px 0;
	    background-image: url(./../../../assets/mine3.png);
	    background-repeat: no-repeat;
	}
	.camera_div_main_bottom>span{
		color: #333;
		font-size: 16px;
		text-align: center;
		font-weight: normal;
	}


	/* 鼠标滑动上传图片样式 */
	.camera_div_main_bottom:hover{
		border: 1px solid #388bff;
	}


	/* 关闭按钮 */
	a.images_close{
		display: block;
		position: absolute;
	    right: 18px;
	    bottom: 13px;
	    z-index: 2;
	    background-position: -20px 0;
	    width: 18px;
	    height: 18px;
	    background-image: url(./../../../assets/mine3.png);
	    background-repeat: no-repeat;
	    cursor: pointer;
	}


	.camera_div_wrap .bg_btn{
		position: relative;
		display: inline-block;
		float: right;
		margin: -1px -1px 0 0;
		width: 100px;
		height: 36px;
		background-color: #3385ff;
		border-bottom: 1px solid #2d78f4;
		cursor: pointer;
	}
	.camera_div_wrap .bg_btn>i{
		display: block;
		position: absolute;
	    margin: auto;
	    left: 0;
	    right: 0;
	    top: 0;
	    bottom: 0;
	    width: 18px;
	    height: 16px;
		background-image: url(./../../../assets/camera_white.png);
	}

	/* 改变input的placeholder的字体颜色 */
	.camera_div_top .bg_wr input[placeholder], [placeholder], *[placeholder] { 
		font-size: 13px!important; 
	} 


	/* 鼠标滑动按钮样式 */
	.camera_div_wrap .bg_btn:hover{
		height: 36px;
		border-bottom: 1px solid #2771d9;
		background-color: #317ef3;
		border-bottom: 1px solid #2868c8;
		box-shadow: 1px 1px 1px #ccc;
	}

	
	/* 选用背景皮肤时，百度按钮的样式 */
	.hasskin_camera_div{
		display: block;
		border: 1px solid rgba(0,0,0,0.2)!important;
	}
	.hasskin_camera_div .bg_btn{
		background-color: rgba(0,0,0,0.2)!important;
		border-bottom: 1px solid rgba(0,0,0,0.2)!important;
		box-shadow: 1px 1px 3px rgba(0,0,0,0.2);
		-webkit-box-shadow: 1px 1px 3px rgba(0,0,0,0.2);
	}
</style>


<template>
	<div 
		class="camera_div_wrap"
		:class="{hasskin_camera_div : changebtnsomestyle}">
		<div class="camera_div_top">
			<div>
				<span class="bg_wr">
					<input 
						:placeholder="cameraPaste">
				</span>
				<span class="bg_btn">
					<i></i>
				</span>
				<div class="div_clear"></div>
			</div>
		</div>
		<div 
			class="camera_div_main">
			<div 
				class="camera_div_main_top">
				<span>拖拽图片到这里</span>
				<i></i>
			</div>
			<div 
				class="camera_div_main_bottom">
				<input>
				<i></i>
				<span>本地上传图片</span>
			</div>
		</div>
		<a 
			class="images_close"
			@click="btnClick"></a>
	</div>
</template>


<script>
	export default{
		props: ['changebtnsomestyle'],
		data(){
			return{
				cameraPaste: '在此处粘贴图片网址'
			}
		},
		methods:{
			btnClick:function(){
				this.$emit('searchModeChange','input')
			}
		}
	}
</script>


