<style>
	.follow_contents_wrap{
		margin: 10px auto;
		width: 844px;
	}
	.follow_contents_title_wrap{
	    z-index: 0;
		position: relative;
	    padding-bottom: 6px;
	    height: 30px;
	    width: 100%;
	    text-align: left;
	    font-size: 14px;
	}
	.follow_contents_title_left{
		padding-bottom: 6px;
		height: 100%;
		line-height: 30px;
		border-bottom: 1px solid rgba(240,240,240,1);
		cursor: pointer;
	}
	.follow_contents_title_left>div{
		float: left;
	}
	.follow_contents_title_icon{
		display: inline-block;
		margin-top: 10px;
		margin-right: 6px;
		position: relative;
		height: 10px;
		width: 10px;
	}
	.follow_contents_title_icon>em{
		position: absolute;
	    top: 3px;
	    left: 0;
	    height: 0;
	    line-height: 0;
	    width: 0;
	    font-size: 0;
	    border: 5px dashed transparent;
	    border-top: 5px solid #666;
	}

	/* em的另外样式 */
	.special_none>em{
		top: 0;
	    left: 3px;
	    border-top: 5px dashed transparent;
	    border-left: 5px solid #666;
	}


	.follow_contents_title_name{
		margin-right: 4px;
		height: 30px;
		line-height: 30px;
	}
	.follow_contents_title_name>span{
		font-weight: bold;
		font-size: 14px;
		text-align: left;
		color: #000;
	}
	.follow_contents_title_counts{
		display: inline-block;
		vertical-align: middle;
		height: 30px;
		color: #666;
	}

	.follow_contents_title_right{
		position: absolute;
	    top: 0;
	    right: 0;
	    height: 30px;
	    line-height: 30px;
	    text-align: right;
	    font-size: 13px;
	}
	.follow_contents_title_right>div{
		display: inline-block;
		float: left;
	    zoom: 1;
	    vertical-align: middle;
	    cursor: pointer;
	    width: 30px;
	    margin-left: 16px;
	    text-align: right;
	}
	.follow_contents_title_right>div>span{
		font-size: 13px;
		font-weight: normal;
		color: #000;
	}

	/* 已经置顶的置顶按钮样式 */
	.follow_contents_title_right .top_index>span{
		color: rgb(153,153,153);
	}


	.follow_contents_bottom{
		display: block;
		position: relative;
		height: 50px;
		width: 840px;
		background-image: url(./../../../../assets/link.png);
		background-repeat: no-repeat;
		background-position: 250px 23px;
	}
	.follow_contents_bottom>div{
		position: absolute;
	    top: 22px;
	    left: 504px;
	    height: 22px;
	    width: 52px;
	    cursor: pointer;
	}
</style>


<template>
	<div>
		<div
			class="follow_contents_wrap"
			v-for="(list,index) in follow_contents"
			@mouseenter="btnsNow=index"
			@mouseleave="btnsNow=-1">
			<div 
				class="follow_contents_title_wrap">
				<div 
					class="follow_contents_title_left"
					@click="showList===index ? showList=-1 : showList=index"
					:style="{borderBottom: '1px solid rgba(240,240,240,'+ changebackgroundopacity +')'}">
					<div 
						class="follow_contents_title_icon"
						:class="index===showList?'':'special_none'">
						<em></em>
					</div>
					<div class="follow_contents_title_name">
						<span>
							{{ list.title }}
						</span>
					</div>
					<div class="follow_contents_title_counts">
						<span>
							(
							{{ list.counts }}
							)
						</span>
					</div>
					<div class="div_clear"></div>
				</div>
				<div 
					class="follow_contents_title_right"
					v-show="index===btnsNow">
					<div>
						<span>添加</span>
					</div>
					<div
						:class="index===0 ? 'top_index' : ''">
						<span>置顶</span>
					</div>
					<div class="div_clear"></div>
				</div>
			</div>
			<div 
				class="follow_contents_bottom"
				v-show="index===showList">
				<div></div>
			</div>
		</div>
	</div>
</template>


<script>
	export default{
		props: ['changebackgroundopacity'],
		data(){
			return{
				showList: '0',
				follow_contents: [
					{
						title: '我的导航',
						counts: '0'
					},
					{
						title: '我的星座',
						counts: '0'
					}
				],
				btnsNow: '0'
			}
		}
	}
</script>


