<style>
	.custom_contents_title>span{
		font-weight: normal;
	    font-size: 13px;
	    color: #666;
	    text-indent: 2px;
	    text-align: left;
	}
	.custom_contents{
		position: relative;
		height: 85px;
		font-size: 0;
		text-align: left;
	}
	.custom_contents>div{
		float: left;
	}


	.custom_nohas_contents{
		position: relative;
		display: inline-block;
		margin: 6px 5px 6px 5px;
		height: 26px;	
		cursor: pointer;
	}
	.custom_nohas_contents_icon{
		display: block;
		position: absolute;
	    top: 9px;
	    left: 9px;
	    width: 8px;
	    height: 8px;
	    overflow: hidden;
	    background-image: url(./../../../assets/mine.png);
	    background-repeat: no-repeat;
	    background-position: -19px -31px;
	}
	.custom_nohas_contents_main{
		padding-left: 7px;
		text-align: center;
	    width: 77px;
	    height: 24px;
	    line-height: 25px;
		border: 1px solid #ccc;
	    background-color: #fff;
	}
	.custom_nohas_contents_main>span{
	    font-size: 13px;
	    color: #222;
	}

	/* 鼠标滑动 */
	.custom_nohas_contents:hover .custom_nohas_contents_icon{
		background-position: -146px -34px;
	}
	.custom_nohas_contents:hover .custom_nohas_contents_main{
		background-color: #38f;
		border: 1px solid #38f;
	}
	.custom_nohas_contents:hover .custom_nohas_contents_main>span{
		color: #fff;
	}
</style>


<template>
	<div>
		<div class="custom_contents_title">
			<span>未订阅频道</span>
		</div>
		<div class="custom_contents">
			<div
				class="custom_nohas_contents"
				v-for="list in followNohasContents">
				<div class="custom_nohas_contents_icon"></div>
				<div class="custom_nohas_contents_main">
					<span>
						{{ list.otherNohasContents }}
					</span>
				</div>
			</div>
			<div class="div_clear"></div>
		</div>
	</div>
</template>


<script>
	export default{
		data() {
			return {
				followNohasContents: [
					{ otherNohasContents: '小说' },
					{ otherNohasContents: '音乐' },
					{ otherNohasContents: '体育' }
				]
			}
		}
	}
</script>


