<style>
	.main_contents_threemaincontents_wrap{
		position: relative;
		height: auto;
	}
</style>


<template>
	<div class="main_contents_threemaincontents_wrap">
		<daohangmaincontents
			v-show="listnumber===0"
			:changebackgroundopacity="(changebackgroundopacity===0 ? 0.2 : changebackgroundopacity)"></daohangmaincontents>
		<tuijianmaincontents
			v-show="listnumber===1"
			:changebackgroundopacity="(changebackgroundopacity===0 ? 0.2 : changebackgroundopacity)"></tuijianmaincontents>
		<wodeguanzhumaincontents
			v-show="listnumber===2"></wodeguanzhumaincontents>
	</div>
</template>


<script>
	import daohangmaincontents from './daohang/daohangmaincontents.vue'
	import tuijianmaincontents from './tuijian/tuijianmaincontents.vue'
	import wodeguanzhumaincontents from './wodeguanzhu/wodeguanzhumaincontents.vue'

	export default{
		props: ['listnumber','changebackgroundopacity'],
		data(){
			return {

			}
		},
		components: {
			daohangmaincontents,
			tuijianmaincontents,
			wodeguanzhumaincontents
		}
	}
</script>


