<style>
  #app {
    min-width: 1000px;
  }
  .topnav{
    width: 100%;
    height: 32px;
    min-width: 1000px;
    border-bottom: 1px solid #ebebeb;
  }


  /* 改变背景图片时改变字体颜色 */
  .topnav.changecolor span.topnav_left_changestyle,
  .topnav.changecolor span.topnav_left_getmessage,
  .topnav.changecolor .topnav_right_wrap>li>a{
    color: #FFF;
    text-decoration: underline;
  }
  .changecolor .topnav_left_div>span{
    color: #fff;
  }


  .topnav_left_wrap{
    float: left;
  }
  .topnav_left_wrap>div{
    float: left;
  }
  .topnav_left_changestyle_wrap,
  .topnav_left_getmessage_wrap{
    display: inline-block;
    position: relative;
    margin:0 10px;
    height: 32px;
    line-height: 32px;
    color: #555;
    text-decoration: underline;
    outline: none;
    text-shadow: none;
    cursor: pointer;
  }
  .topnav_left_changestyle,
  .topnav_left_getmessage{
    font-size: 12px;
  }

  /* css动画 */
  /* 向下滑动显示，向上滑动消失 */
  .dropshow-enter-active{
    transition: all .5s ease-out;
  }
  .dropshow-leave-active{
    transition: all .5s ease-out;
  }
  .dropshow-enter,
  .dropshow-leave-active{
    transform: translateY(-300px);
    opacity: 0;
  }
</style>


<template>
	<!-- 头部 -->
  <div 
    class="topnav"
    :class="{ changecolor : changeword }">
    <div class="topnav_left_wrap">
      <weather></weather>
      <div 
        class="topnav_left_changestyle_wrap"
        @click.top="changestyle">
        <span class="topnav_left_changestyle">换肤</span>
      </div>
      <transition name="dropshow">
        <changestylesubcontents 
          v-show="showgetstylesubcontents"
          @changeskin="showChangeSkin"
          @changebackgroundimg="backgroundImgSrc"
          @changesomestyle="changeStyle"
          @changetopwordcolor="topwordColor"
          @changebackgroundopacity="changeBackgroundOpacity"
          @clickrestractbtns="clickRestractBtn"></changestylesubcontents>
      </transition>
      <div 
        class="topnav_left_getmessage_wrap"
        @click.stop="changemessagesubcontents">
        <span class="topnav_left_getmessage">消息</span>
        <getmessagesubcontents
          v-show="showgetmessagesubcontents"
          @click.stop="stopchangemessagesubcontents"></getmessagesubcontents>
      </div>
    </div>
    <toprightnav 
      :userclickoutsetupside="userclickoutsetupside"
      @delivernewuserclick="delivernewuserclicksetup"></toprightnav>
    <div class="div_clear"></div>
  </div>
</template>


<script>
	import weather from './weather.vue'
	import toprightnav from './toprightnav.vue'
  import getmessagesubcontents from './getmessagesubcontents.vue'
  import changestylesubcontents from './changestylesubcontents.vue'

	export default{
    props: ['userstyleclose','userclickclose','userclickoutsetupside'],
		data(){
			return {
        // 消息
        showgetmessagesubcontents: this.userclickclose,//①创建props属性result的副本--showgetmessagesubcontents
        // 换肤
        showgetstylesubcontents: this.userstyleclose,//①创建props属性result的副本--showgetstylesubcontents
        // 设置
        userclickoutsetupside: this.userclickoutsetupside,
        detailsrc: '',
        changeword: false,
			}
		},
    methods: {
      showChangeSkin: function(showchangestyle){
        this.showchangestyle = showchangestyle;
      },
      backgroundImgSrc: function(src){
        this.$emit('changebackgroundimg',src);
        this.changeword = true;
      },
      topwordColor: function(recoverycolor){
        this.changeword = recoverycolor;
      },
      changeStyle: function(change){
        this.$emit('changesomestyle',change);
      },
      changeBackgroundOpacity: function(backgroundopacity){
        this.$emit('changebackgroundopacity',backgroundopacity);
      },
      // 消息
      changemessagesubcontents: function(){
        this.showgetmessagesubcontents=!this.showgetmessagesubcontents;
      },
      // 换肤
      changestyle: function(){
        this.showgetstylesubcontents=!this.showgetstylesubcontents;
      },
      // 皮肤界面点击收起按钮
      clickRestractBtn: function(val){
        this.showgetstylesubcontents=val;
      },
      // 设置
      delivernewuserclicksetup(val){
        this.$emit('delivernewclosesetup',val);
      }
    },
    watch:{
      // 消息
      userclickclose(val){
        this.showgetmessagesubcontents = val;//②监听外部对props属性showgetmessagesubcontents的变更，并同步到组件内的data属性showgetmessagesubcontents中
      },
      showgetmessagesubcontents(val){
        this.$emit('delivernewuserclick',val);//③组件内对showgetmessagesubcontents变更后向外部发送事件通知
      },
      // 换肤
      userstyleclose(val){
          this.showgetstylesubcontents = val;//②监听外部对props属性showgetstylesubcontents的变更，并同步到组件内的data属性showgetstylesubcontents中
      },
      showgetstylesubcontents(val){
        this.$emit('delivernewchangestyle',val);//③组件内对showgetstylesubcontents变更后向外部发送事件通知
      }
    },
		components: {
		  weather,
		  toprightnav,
      getmessagesubcontents,
      changestylesubcontents
		}
	}
</script>


