<style>
	.danghang_contents1>div{
		margin: 8px 0 4px;
		width: 100%;
	}
	.danghang_contents1>div>span{
		font-size: 14px;
		text-align: left;
		font-weight: bold;
		color: #000;
	}
	.danghang_contents1>ul>li{
		float: left;
		display: inline-block;
		position: relative;
		margin-right: 15px;
		margin-top: 15px;
		cursor: pointer;
	}
	.danghang_contents1>ul>li>a{
		display: block;
		position: relative;
		height: 58px;
		width: 126px;
		line-height: 58px;
		background-color: #fcfcfc;
	}
	.danghang_contents1>ul>li>a>img{
		position: absolute;
	    left: 20px;
	    top: 50%;
	    margin-top: -8px;
	    height: 16px;
	    width: 16px;
	}
	.danghang_contents1>ul>li>a>span{
		position: absolute;
	    text-align: left;
	    left: 44px;
	    color: #333;
	    font-size: 14px;
	    padding: 0;
	    max-width: 71px;
	    _width: 71px;
	    overflow: hidden;
	    white-space: nowrap;
	    text-overflow: ellipsis;
	    display: inline-block;
	}

	/* 鼠标滑动 */
	.danghang_contents1>ul>li:hover>a{
		margin: -1px;
		border: 1px solid rgba(0,0,0,.05);
	}
</style>


<template>
	<div class="danghang_contents1">
		<div>
			<span>
				{{ title }}
			</span>
		</div>
		<ul>
			<li
				v-for="list in appLists"
				:title="list.appName">
				<a>
					<img
						:src="list.appImgUrl">
					<span>
						{{ list.appName }}
					</span>
				</a>
			</li>
			<div class="div_clear"></div>
		</ul>
	</div>
</template>


<script>
	export default{
		data(){
			return {
				title: '猜你喜欢',
				appLists: [
					{
						appImgUrl: require('./../../../../assets/nuomi.png'),
						appName: '百度糯米'
					},
					{
						appImgUrl: require('./../../../../assets/jingdong.png'),
						appName: '京东商城'
					},
					{
						appImgUrl: require('./../../../../assets/1haodian.png'),
						appName: '1号店'
					},
					{
						appImgUrl: require('./../../../../assets/suningyigou.png'),
						appName: '苏宁易购'
					},
					{
						appImgUrl: require('./../../../../assets/weipinhui.png'),
						appName: '唯品会'
					},
					{
						appImgUrl: require('./../../../../assets/dangdangwang.png'),
						appName: '当当网'
					}
				]
			}
		}
	}
</script>


