<style>
	.setupsubcontents_wrap{
		z-index: 500;
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		min-width: 1000px;
		min-height: 300px;
		background-color: #fafafa;
		overflow: hidden;
	}
	.setupsubcontents{
		margin: 0 60px;
	}
	.setupsubcontents_btns_wrap{
		clear: both;
		height: 54px;
		line-height: 54px;
		border-bottom: 1px solid #e6e6e6;
		list-style: none;
	}
	.setupsubcontents_btns{
		float: left;
		padding: 0 10px;
		margin-right: 10px;
		cursor: pointer;
	}
	.setupsubcontents_btns_text{
		color: #666;
		font-weight: normal;
		font-size: 14px;
	}
	/* 关闭按钮 */
	.setupsubcontents_closebtns{
		float: right;
		padding: 0 10px;
		margin-top: 20px;
		cursor: pointer;
	}
	.setupsubcontents_closebtns_icon{
		display: block;
		width: 16px;
		height: 16px;
		background-image: url(./../../assets/icons.png);
		background-repeat: no-repeat;
		background-position: -248px 0;
		background-size: 300px 18px;
	}

	/* 鼠标点击btns标题附加样式 */
	.setupsubcontents_clickbtns{
		margin-bottom: -1px;
		height: 52px;
		border-bottom: 2px solid #2c85ff;
		color: #333;
		font-weight: bold;
	}
	.setupsubcontents_clickbtns .setupsubcontents_btns_text{
		font-weight: bold;
	}


	.setupsubcontents_contents_wrap{
		margin-top: 20px;
	}
	.setupsubcontents_contents{

	}
	.setupsubcontents_contents_main{
		height: 33px;
		line-height: 33px;
	}
	.setupsubcontents_contents_main .setupsubcontents_title{
		margin-left: 50px;
		margin-right: 10px;
		font-weight: bold;
	}
	.setupsubcontents_contents_main .setupsubcontents_title,
	.setupsubcontents_contents_main .setupsubcontents_require{
		float: left;
		font-size: 13px;
		cursor: default;
	}
	.setupsubcontents_contents_main .setupsubcontents_option_close{
		margin-right: 500px;
	}
	.setupsubcontents_contents_main .setupsubcontents_option_open{
		margin-right: 10px;
	}
	.setupsubcontents_contents_main .setupsubcontents_option_open,
	.setupsubcontents_contents_main .setupsubcontents_option_close{
		float: right;
		display: block;
		height: 33px;
		line-height: 33px;
		vertical-align: middle;
		cursor: pointer;
	}
	.setupsubcontents_option_open .open_input,
	.setupsubcontents_option_close .close_input{
		margin-right: 1px;
		margin-top: -2px;
		vertical-align: middle;
		cursor: pointer;
	}
	.setupsubcontents_option_open .text,
	.setupsubcontents_option_close .text{
		font-size: 13px;
		color: #000;
		font-weight: normal;
		user-select: none;
		cursor: pointer;
	}

	/* 鼠标滑动每一个li */
	.setupsubcontents_contents .setupsubcontents_contents_main:hover{
		background-color: #F5F5F5;
	}

	.setupsubcontents_savebtn{
		padding-top: 20px;
		margin: 0 auto;
		width: 90px;
		height: 28px;
		cursor: pointer;
	}
	.setupsubcontents_savebtn .setupsubcontents_clickbtns_text{
		display: block;
		width: 90px;
		height: 26px;
		line-height: 26px;
		text-align: center;
		background-color: #38f;
		color: #fff;
		font-size: 13px;
		border-top: 1px solid #38f;
		border-bottom: 1px solid #38f;
		text-decoration: none;
	}

	/* 鼠标滑动到保存设置 */
	.setupsubcontents_savebtn:hover .setupsubcontents_clickbtns_text{
		background-color: #3180f7;
		border-top: 1px solid #3180f7;
		border-bottom: 1px solid #3180f7;
	}
</style>


<template>
	<div 
		class="setupsubcontents_wrap" 
		@click.stop>
		<div class="setupsubcontents">
			<ul class="setupsubcontents_btns_wrap">
				<li
					v-for="(list,index) in setuptitlebtns"
					class="setupsubcontents_btns"
					:class="clicksetupbtns===index ? 'setupsubcontents_clickbtns' : ''"
					@click.stop="clicksetupbtns=index">
					<span class="setupsubcontents_btns_text">{{ list.btnsname }}</span>
				</li>
				<li class="setupsubcontents_closebtns">
					<span 
						class="setupsubcontents_closebtns_icon"
						@click="closesetupsubcontents"></span>
				</li>
			</ul>
			<div 
				class="setupsubcontents_contents_wrap"
				v-show="clicksetupbtns===2">
				<ul class="setupsubcontents_contents">
					<li
						class="setupsubcontents_contents_main"
						v-for="list in setupcontentsmain">
						<a 
							class="setupsubcontents_title"
							:title="list.title">
							{{ list.title }}:
						</a>
						<a 
							class="setupsubcontents_require"
							:title="list.require">
							{{ list.require }}
						</a>
						<div class="setupsubcontents_option_close">
							<input 
								type="radio" 
								value="关闭" 
								class="close_input"
								:name="list.inputname"
								:id="list.closeid"
								:checked="list.close_checked">
							<label 
								class="text"
								:for="list.closeid">关闭</label>
						</div>
						<div class="setupsubcontents_option_open">
							<input 
								type="radio" 
								value="打开"
								class="open_input"
								:name="list.inputname"
								:id="list.openid"
								:checked="list.open_checked">
							<label 
								class="text"
								:for="list.openid">打开</label>
						</div>
						<div class="div_clear"></div>
					</li>
				</ul>
				<div class="setupsubcontents_savebtn">
					<a class="setupsubcontents_clickbtns_text">保存设置</a>
				</div>
			</div>
		</div>
	</div>
</template>


<script>
	export default{
		data(){
			return {
				setuptitlebtns: [
					{ btnsname: '搜索设置' },
					{ btnsname: '高级搜索' },
					{ btnsname: '首页设置' }
				],
				clicksetupbtns: '',
				setupcontentsmain: [
					{
						title: '天气模块',
						require: '是否展示天气预报',
						inputname: 'weather',
						closeid: 'close_weather',
						openid: 'open_weather',
						open_checked: 'checked'
					},
					{
						title: '搜索模块',
						require: '搜索结果是否在新窗口中打开',
						inputname: 'search',
						closeid: 'close_search',
						openid: 'open_search',
						close_checked: 'checked'
					},
					{
						title: '换肤活动',
						require: '是否开启换肤活动提醒',
						inputname: 'changestyle',
						closeid: 'close_changestyle',
						openid: 'open_changestyle',
						open_checked: 'checked'
					}
				],
				closesetupsubmenu: false
			}
		},
		methods: {
			closesetupsubcontents: function(){
				this.$emit('closesetupsubcontentmenu',this.closesetupsubmenu);
			}
		}
	}
</script>


