import Vue from 'vue'
import Router from 'vue-router'
import top from '@/components/topcolumn/top'
import centermain from '@/components/centermaincolumn/centermain'
import footercolumn from '@/components/footercolumn/footercolumn'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'top',
      component: top
    },
    {
      path: '/',
      name: 'centermain',
      component: centermain
    },
    {
      path: '/',
      name: 'footercolumn',
      component: footercolumn
    }
  ]
})



