<style>
	p{
		margin: 0;
		padding: 0;	
	}
	.skinpicframe_wrap{
		position: relative;
		float: left;
		width: 600px;
		height: 242px;
	}
	.skinpicframe_img{
		position: relative;
		width: 100%;
		height: 242px;
	}
	.skin_img_item{
		position: absolute;
	    cursor: pointer;
	    width: 97px;
	    height: 59px;
	    background-color: #f5f5f5;
	}
	.skin_img_item>img{
		width: 97px;
		height: 59px;
	}
	.skin_img_shadow{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: #3c3c3c;
		opacity: .6;
	}
	.skin_img_text{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		line-height: 59px;
		text-align: center;
		font-size: 13px;
		color: #fff;
	}
	.skinpic_icon{
		position: absolute;
		bottom: 4px;
		right: 4px;
		display: block;
		width: 18px;
		height: 18px;
		font-size: 13px;
		background-image: url(./../../assets/skin_icon.png);
		background-repeat: no-repeat;
		background-position: -180px 0;
	}


	/* CSS样式 */
	.fade-enter-active,
	.fade-leave-active{
		transition: opacity .5s ease-out;
	}
	.fade-enter,
	.fade-leave-active{
		opacity: 0;
	}


	/* 选加样式 */
	.skinpicframe_img .big_img,
	.skinpicframe_img .big_img>img{
		width: 195px;
		height: 119px;
	}

	.position_1{
		top: 0;
		left: 0;
	}
	.position_1 .skin_img_text{
		line-height: 119px;
	}
	.position_2{
		top: 120px;
		left: 0;
	}
	.position_3{
		top: 120px;
		left: 98px;
	}
	.position_4{
		top: 0;
		left: 196px;
	}
	.position_5{
		top: 60px;
		left: 196px;
	}
	.position_6{
		top: 120px;
		left: 196px;
	}
	.position_7{
		top: 0;
		left: 294px;
	}
	.position_8{
		top: 0;
		left: 392px;
	}
	.position_9{
		top: 60px;
		left: 294px;
	}
	.position_9 .skin_img_text{
		line-height: 119px;
	}
	.position_10{
		top: 0;
		left: 490px;
	}
	.position_11{
		top: 60px;
		left: 490px;
	}
	.position_12{
		top: 120px;
		left: 490px;
	}
</style>


<template>
	<div class="skinpicframe_wrap">
		<ul class="skinpicframe_img">
			<li
				class="skin_img_item"
				v-for="(list,index) in skinpic"
				:class="list.className"
				@click="selectedBackgroundSkin(index,list.skinImgUrl)"
				@mouseenter="showText(index,list.skinImgUrl)"
				@mouseleave="hideText(index,list.skinImgUrl)">
				<img :src="list.skinImgUrl">
				<transition name="fade">
					<div v-show="showtext === index">
						<div 
							:class="list.shadowClassName"></div>
						<p class="skin_img_text">
							{{ list.pictext }}
						</p>
					</div>
				</transition>
				<div 
					class="skinpic_icon"
					v-show="list.skinImgUrl === confirmselectedrecentusedskin"></div>
			</li>
			<div class="div_clear"></div>
		</ul>
		<div class="div_clear"></div>
	</div>
</template>


<script>
	export default{
		props: ['confirmselectedrecentusedskin','skinpic','normalskinbtn'],
		data(){
			return {
				lastimgsrc: '', // 用于接收上一次图片的地址
				useskin: true,	// 使用皮肤图片
				seeskin: false,	// 查看皮肤图片
				selected: false, // 是否已经选择皮肤图片
				changestyle: true, // 是否改变皮肤
				selectedskin: '', // 选择皮肤
				showtext: '', // 显示对应文本
				saveselectedpic: '', // 保存选择的图片地址
			}
		},
		methods: {
			selectedBackgroundSkin: function(index,src){
				if(this.lastimgsrc==''){
					this.lastimgsrc=src;
					this.$emit('changebackgroundimg',src);
				}else{
					if(this.lastimgsrc==src){
					}else{
						this.lastimgsrc=src;
						this.$emit('changebackgroundimg',src);
					}
				}
				this.selectedskin = index;
				this.saveselectedpic = src;
				this.selected = true;
				this.$emit('showpicdetail',src);
				//this.$emit('checksrc',src); // 最近使用检查src路径与之前src路径是否相同
				this.$emit('showtoolbar',this.useskin);
				this.$emit('changesomestyle',this.changestyle);
			},
			showText: function(index,src){
				this.showtext = index;
				this.seeskin = true;
				this.$emit('showpicdetail',src);
				this.$emit('seepicdetail',this.seeskin);
			},
			hideText: function(index,src){
				this.showtext = ''; // 隐藏对应文本
				// 判断是否已经选择皮肤图片
				//     如果没有选择皮肤图片，鼠标离开时查看详细图片窗口为空
				//     如果选择皮肤图片，鼠标离开时查看详细图片窗口为已经选择的图片
				if(!this.selected){
					this.seeskin = false;
					this.$emit('showpicdetail',src);
					this.$emit('seepicdetail',this.seeskin);
				}else{
					this.$emit('showpicdetail',this.confirmselectedrecentusedskin);
				}
			}
		},
		watch: {
		}
	}
</script>


