<style>
	.topdropmenu_wrap{
		position: absolute;
		top: 32px;
		left: 50%;
		margin-left: -25px;
		width: 70px;
		background-color: #fff!important;
		border: 1px solid #fff;
		box-shadow: 1px 1px 5px #d1d1d1;
		-webkit-box-shadow: 1px 1px 5px #d1d1d1;
	}
	.topdropmenu_list{
		display: block;
		padding-left: 11px;
		height: 25px;
		line-height: 22px;
		cursor: pointer;
	}
	.topdropmenu_list>a{
		text-align: center;
		color: #000!important;
		font-size: 12px;
		text-decoration: none;
	}

	
	/* 鼠标滑动样式 */
	.topdropmenu_wrap .topdropmenu_list:hover{
		background-color: #38F;
	}
	.topdropmenu_wrap .topdropmenu_list:hover>a{
		color: #FFF!important;
	}


	.topdropmenu_top{
		position: absolute;
	    z-index: 2;
	    top: -11px;
	    left: 28px;
	    display: inline-block;
	    width: 0;
	    height: 0;
	    line-height: 0;
	    border: 5px dashed transparent;
	    border-bottom: 5px solid #e3e3e3;
	    font-size: 0;
	}
	.topdropmenu_top em{
		position: absolute;
	    top: -4px;
	    left: -5px;
	    display: block;
	    width: 0;
	    height: 0;
	    line-height: 0;
	    font-size: 0;
	    color: #666;
	    border-top: 5px dashed transparent;
	    border-left: 5px dashed transparent;
	    border-right: 5px dashed transparent;
	    border-bottom: 5px solid #fff;
	}
</style>


<template>
	<ul class="topdropmenu_wrap">
		<div class="topdropmenu_top">
			<em></em>
		</div>
		<li 
			v-for="list in message"
			class="topdropmenu_list">
			<a>{{ list.submenu }}</a>
		</li>
	</ul>
</template>


<script>
	export default{
		props: ['message'],
		data(){
			return{
			}
		}
	}
</script>


