<style>
	.main_contents_right_contents_wrap{
		clear: both;
		width: 300px;
	}
	.main_contents_right_contents_wrap li.main_contents_right_contents{
		float: left;
		width: 145px;
		height: 34px;
	    margin-right: 3px;
	    display: inline-block;
	}
	.main_contents_right_contents_wrap li.main_contents_right_contents span.contents_text{
		float: left;
		color: #333;
		font-size: 14px;
		line-height: 34px;
		font-weight: normal;
		white-space: nowrap;
	}
	.main_contents_right_contents_wrap li.main_contents_right_contents .icon{
		display: inline-block;
		margin-top: 7px;
		margin-left: 7px;
		width: 18px;
		height: 20px;
		background-image: url(./../../../../assets/news.png);
		background-repeat: no-repeat;
		background-position: -57px -98px;
	}
	.main_contents_right_contents_wrap li.main_contents_right_contents span.contents_text .hotspots_text{
		cursor: pointer;
	}

	/* 鼠标滑过 */
	.main_contents_right_contents_wrap a.hotspots_text:hover{
		color: #0079f4;
		text-decoration: underline;
	}
</style>


<template>
	<ul class="main_contents_right_contents_wrap">
		<li
			class="main_contents_right_contents"
			v-show="hotspotshow===index"
			v-for="(list,index) in message">
			<span class="contents_text">
				<a class="hotspots_text">
					{{ list.hotspotname }}
				</a>
			</span>
			<span
				:class="list.hotspoticon"></span>
		</li>
		<div class="div_clear"></div>
	</ul>
</template>


