<style>
</style>


<template>
	<form class="searchfrom">
		<searchinformationinput 
			@searchModeChange="inputModeChange"
			:changebtnsomestyle="changebtnbackgroundimg" 
			v-show="searchMode==='input'"></searchinformationinput>
		<camerapaste 
			@searchModeChange="inputModeChange" 
			:changebtnsomestyle="changebtnbackgroundimg"
			v-show="searchMode==='image'"></camerapaste>
		<div class="div_clear"></div>
	</form>
</template>


<script>
	import camerapaste from './camerapaste.vue'
	import searchinformationinput from './searchinformationinput.vue'

	export default{
		props: ['changebtnbackgroundimg'],
		data(){
			return {
        		searchMode: 'input'
			}
		},
		methods:{
			inputModeChange:function(status){
				this.searchMode=status;
			}
	    },
	    components: {
	    	camerapaste,
			searchinformationinput
	    }
	}
</script>


