<style>
	.custom_wrap{
		position: relative;
	    margin-top: 13px;
	    height: 30px;
	    line-height: 30px;
	    text-align: left;
	}
	.custom_title{
	    margin-left: 25px;
		height: 30px;
	    line-height: 30px;
	    width: 60px;
	    cursor: pointer;
	}
	.custom_title>span{
		float: left;
	}
	.custom_title_icon{
		display: inline-block;
		margin-top: 9px;
		height: 12px;
	    width: 12px;
	    font-size: 0;
	    background-image: url(./../../../../assets/mine.png);
	    background-repeat: no-repeat;
	    background-position: 0 0;
	}
	.custom_title_text{
		display: inline-block;
		padding-left: 6px;
		font-size: 14px;
		color: #333;
	}


	/* 鼠标点击样式 */
	.custom_title_text_click{
		color: #07f;
	}
	.custom_title_icon_click{
		background-position: -70px 0;
	}


	.custom_main{
		position: relative;
		left: 29px;
		display: block;
		width: 653px;
		height: 134px;
		background-color: #fff;
		text-align: left;
		border: 1px solid #e3e3e3;
		box-shadow: 1px 2px 1px 0 rgba(0,0,0,0.072);
		-webkit-box-shadow: 1px 2px 1px 0 rgba(0,0,0,0.072);
	}
	.custom_main>a{
		display: block;
		position: absolute;
		top: 10px;
		right: 12px;
		height: 11px;
		width: 11px;
		background-image: url(./../../../../assets/mine.png);
		background-repeat: no-repeat;
		background-position: -163px -28px;
		cursor: pointer;
	}
	.custom_main_icon{
	    display: inline-block;
		position: absolute;
	    z-index: 2;
	    top: -12px;
	    left: 28px;
	    width: 0;
	    height: 0;
	    line-height: 0;
	    font-size: 0;
	    border: 6px dashed transparent;
	    border-bottom: 6px solid #e3e3e3;
	}
	.custom_main_icon>em{
	    display: block;
		position: absolute;
	    top: -5px;
	    left: -6px;
	    width: 0;
	    height: 0;
	    line-height: 0;
	    font-size: 0;
	    color: #666;
	    border-top: 6px dashed transparent;
	    border-left: 6px dashed transparent;
	    border-right: 6px dashed transparent;
	    border-bottom: 6px solid #fff;
	}
	.custom_main_contents{
		height: 121px;
	}
	.custom_main_contents>div{
	    zoom: 1;
		float: left;
	    position: relative;
	    padding-left: 31px;
	    width: 280px;
	}


	.custom_has_contents{
		position: relative;
		display: inline-block;
		margin: 6px 5px 6px 5px;
		cursor: pointer;
	}
	.custom_has_contents_icon{
		display: block;
		position: absolute;
	    top: -6px;
	    left: -6px;
	    width: 15px;
	    height: 15px;
	    overflow: hidden;
	    background-image: url(./../../../../assets/mine.png);
	    background-repeat: no-repeat;
	    background-position: 0 -13px;
	}
	.custom_has_contents_main{
	    height: 24px;
		width: 79px;
	    line-height: 25px;
		text-align: center;
	    background-color: #fff;
		border: 1px dashed #cdcdcd;
	}
	.custom_has_contents_main>span{
	    color: #222;
	    font-size: 13px;
	}

	/* 鼠标滑动 */
	.custom_has_contents:hover .custom_has_contents_icon{
		background-position: -16px -13px;
	}
	.custom_has_contents:hover .custom_has_contents_main{
		border:1px dashed #38f;
	}
	.custom_has_contents:hover .custom_has_contents_main>span{
		color: #919191;
	}
</style>


<template>
	<div>
		<dropfollow :changebackgroundopacity="changebackgroundopacity"></dropfollow>
		<div class="custom_wrap">
			<div 
				class="custom_title"
				@click="customNow===1 ? customNow=0 : customNow=1">
				<span 
					class="custom_title_icon"
					:class="customNow===1 ? 'custom_title_icon_click' : ''"></span>
				<span 
					class="custom_title_text"
					:class="customNow===1 ? 'custom_title_text_click' : ''">自定义</span>
				<div class="div_clear"></div>
			</div>
			<div 
				class="custom_main"
				v-show="customNow===1">
				<a
					@click="customNow=0"></a>
				<div class="custom_main_icon">
					<em></em>
				</div>
				<div class="custom_main_contents">
					<div>
						<div class="custom_contents_title">
							<span>已关注</span>
						</div>
						<div class="custom_contents">
							<div
								class="custom_has_contents"
								v-for="list in follow_has_contents">
								<div class="custom_has_contents_icon"></div>
								<div class="custom_has_contents_main">
									<span>
										{{ list.name }}
									</span>
								</div>
							</div>
							<div class="div_clear"></div>
						</div>
					</div>
					<customright></customright>
				</div>
			</div>
		</div>
	</div>
</template>


<script>
	import dropfollow from './dropfollow'
	import customright from './customright'

	export default{
		props: ['changebackgroundopacity'],
		data(){
			return{
				follow_has_contents: [
					{ name: '我的导航' },
					{ name: '我的星座' }
				],
				customNow: '0'
			}
		},
		components: {
			dropfollow,
			customright
		}
	}
</script>


