<style>
  body{
    margin: 0;
    padding: 0;
  }
  ul{
    list-style: none;
    margin: 0;
    padding: 0;
  }
  .div_clear{
    clear: both;
    height: 0;
  }
  .app{
    display: block;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: cover;
    -webkit-background-size: cover;
  }
</style>


<template>
  <div 
    id="app"
    class="app" 
    @click="userclick"
    @mousewheel="mouseScrolling"
    :style="{backgroundImage : 'url('+ img +')'}">
    <!-- 顶部 -->
    <top
      @changebackgroundimg="backgroundImgSrc"
      @changesomestyle="changeStyle"
      @changebackgroundopacity="changeBackgroundOpacity"
      @delivernewuserclick="newUserClick"
      @delivernewchangestyle="newChangeStyle"
      @delivernewclosesetup="newCloseSetup"
      :userclickclose="userclicksub"
      :userstyleclose="userchangestyle"
      :userclickoutsetupside="userclickoutsetupside"></top>
    <!-- 中间主要部分 -->
    <centermain
      :changecentersomestyle="changecenterstyle"
      :mousedirection="wheeldown"
      :changebackgroundopacity="backgroundopacity"></centermain>
    <!-- 底部 -->
    <footercolumn
      :changefooterwordcolor="changefooterword"></footercolumn>
  </div>
</template>

<script>
  import top from './components/topcolumn/top.vue'
  import centermain from './components/centermaincolumn/centermain.vue'
  import footercolumn from './components/footercolumn/footercolumn.vue'

  export default {
    name: 'App',
    data(){
      return {
        img: '',
        hotimgs: [
          { hotimg: './assets/background_hotimg01.jpg' },
          { hotimg: './assets/background_hotimg02.jpg' },
          { hotimg: './assets/background_hotimg03.jpg' },
          { hotimg: './assets/background_hotimg04.jpg' },
          { hotimg: './assets/background_hotimg05.jpg' },
          { hotimg: './assets/background_hotimg06.jpg' }
        ],
        changecenterstyle: false,
        changefooterword: false,
        wheeldown: false,
        backgroundopacity: '',
        
        userclicksub: false,
        userchangestyle: false,
        userclickoutsetupside: false
      }
    },
    methods: {
      mouseScrolling: function(event){
        if(event.deltaY<0 && this.wheeldown==false){
          this.wheeldown = false;
        }else{
          this.wheeldown = true;
        }
      },
      backgroundImgSrc: function(detailsrc){
        this.img = detailsrc;
      },
      changeStyle: function(change){
        this.changecenterstyle = change;
        this.changefooterword = change;
      },
      changeBackgroundOpacity: function(backgroundopacity){
        this.backgroundopacity = backgroundopacity;
      },
      // 点击body收缩一切显示的窗口
      userclick: function(){
        this.userclicksub=false;
        this.userchangestyle=false;
        this.userclickoutsetupside=false;
        console.log(this.userclickoutsetupside)
        //console.log(this.clickbodywrap)
      },
      // 消息
      newUserClick: function(sub){
        this.userclicksub=sub;//④外层调用组件方注册变更方法，将组件内的数据变更，同步到组件外的数据状态中
      },
      // 换肤
      newChangeStyle: function(val){
        this.userchangestyle=val;//④外层调用组件方注册变更方法，将组件内的数据变更，同步到组件外的数据状态中
      },
      // 设置
      newCloseSetup: function(val){
        this.userclickoutsetupside=val;
        console.log(val)
      }
    },
    components: {
      top,
      centermain,
      footercolumn
    }
   /* wwwww:function(e){
      this.$refs.c1.$refs.c2
    }*/
    
  }
</script>


