<style>
	.main_bottom_text_wrap{
		margin-top: 10px;
	}
	.main_bottom_text_wrap>div{
		text-align: center;
	}
	.main_bottom_text_wrap span{
		color: #999;
		font-size: 12px;
	}
	.main_bottom_text_wrap img{
		position: relative;
		top: 2px;
	}
	.main_bottom_text_wrap a{
		cursor: auto;
		margin-right: 5px;
	}
	.bottom_police{
		margin: 0 auto;
		width: 196px;
		cursor: pointer;
	}
	.bottom_police .bottom_police_icon{
		position: relative;
		top: 3px;
		display: inline-block;
		margin-right: 6px;
		width: 14px;
		height: 16px;
		background-image: url(./../../assets/icon-police.png);
		background-repeat: no-repeat;
		background-position: 0 0;
	}

	/* 有皮肤背景时，底部字体颜色变白 */
	.main_bottom_changewordcolor span{
		color: #FFF;
	}

	/* 鼠标滑动 */
	.bottom_police:hover .bottom_police_text{
		text-decoration: underline;
	}

	.text_underline{
		cursor: pointer;
		text-decoration: underline;
	}
</style>


<template>
	<div
		:class="{main_bottom_changewordcolor : changefooterwordcolor}" 
		class="main_bottom_text_wrap">
		<div>
			<span>
				<a class="text_underline">
					<span>设为首页</span>
				</a>
				<a>
					<span>
						{{ baiduDate }}
					</span>
				</a>
				<a class="text_underline">
					<span>使用百度前必读</span>
				</a>
				<a class="text_underline">
					<span>意见反馈</span>
				</a>
				<a>
					<span>
						{{ ICPNumber }}
					</span>
				</a>
				<img
					:src="policeImgUrl">
			</span>
		</div>
		<div class="bottom_police">
			<span class="bottom_police_icon"></span>
			<span class="bottom_police_text">
				{{ policeNumber }}
			</span>
		</div>
	</div>
</template>


<script>
	export default{
		props: ['changefooterwordcolor'],
		data(){
			return{
				baiduDate: '©2018 Baidu',
				ICPNumber: '京ICP证030173号 ',
				policeImgUrl: require('@/assets/copy.png'),
				policeNumber: '京公网安备11000002000001号'
			}
		}
	}
</script>


