<style>
	.main_contents .setup_contents_wrap{
	    display: block;
	    position: absolute;
	    top: 40px;
	    left: 0px;
	    padding: 22px 25px 0 25px;
		width: 849px;
	    height: 186px;
	    text-align: left;
	    border: 1px solid #e3e3e3;
	    background-color: #fff;
	    box-shadow: 1px 2px 1px 0 rgba(0,0,0,0.072);
	    -webkit-box-shadow: 1px 2px 1px 0 rgba(0,0,0,0.072);
	}
	.setup_contents_top{
		height: 121px;
		border-bottom: 1px solid #f0f0f0;
	}
	.setup_contents_top_left{
		position: relative;
		float: left;
		width: 421px;
	}
	.setup_contents_top_left_title>span{
		font-weight: normal;
	    font-size: 12px;
	    color: #666;
	    text-indent: 2px;
	}
	.setup_contents_top_left_name{
		float: left;
	    width: 78px;
	    text-align: center;
	    margin-top: 18px;
	    border: 1px solid #cdcdcd;
	    height: 24px;
	    line-height: 24px;
	}
	.setup_contents_top_left_name>span{
		color: #666;
		font-size: 13px;
		font-weight: normal;
	}
	.setup_contents_top_left_contents_wrap{
		position: relative;
		margin-left: 85px;
		margin-top: 8px;
	}
	.setup_contents_top_left_contents{
		position: relative;
		float: left;
		margin: 10px 8px 10px 8px;
		cursor: pointer;
	}
	.setup_contents_top_left_contents .icon{
		display: block;
		position: absolute;
	    top: -6px;
	    left: -6px;
	    width: 15px;
	    height: 15px;
	    overflow: hidden;
	    background-image: url(./../../../assets/mine2.png);
	    background-repeat: no-repeat;
	    background-position: -40px -25px;
	}
	.setup_contents_top_left_contents .text{
	    height: 24px;
	    line-height: 24px;
	    width: 53px;
		text-align: center;
		border: 1px dashed #cdcdcd;
	    background-color: #fff;
	}
	.setup_contents_top_left_contents .text>span{
	    font-size: 12px;
		color: #222;
	}

	/* 鼠标滑动 */
	.setup_contents_top_left_contents:hover .icon{
		background-position: -21px -25px;
	}
	.setup_contents_top_left_contents:hover .text{
		border: 1px dashed #38f;
	}
	.setup_contents_top_left_contents:hover .text>span{
		color: #919191;
	}


	.setup_contents_top_right{
		float: right;
	    position: relative;
	    width: 421px;
	}
	.setup_contents_top_right .custom_contents{
		margin-top: 8px;
	}
	.setup_contents_top_right .custom_nohas_contents_main{
		width: 53px;
	}


	.setup_contents_bottom{
		position: relative;
		height: 40px;
	}
	.setup_contents_bottom>span{
		float: right;
		position: absolute;
	    right: 0;
	    bottom: 13px;
	    display: inline-block;
	    width: 17px;
	    height: 17px;
	    vertical-align: top;
	    font-size: 12px;
	    background-image: url(./../../../assets/mine2.png);
	    background-repeat: no-repeat;
	    background-position: -147px -24px;
	}
	.hide_mods{
		float: right;
	    margin-left: 10px;
	    margin-right: 21px;
	    margin-top: 10px;
	    line-height: 15px;
	    font-size: 13px;
	    color: #222;
	    cursor: pointer;
	}
	.hide_mods>span{
		float: left;
	}
	.hide_mods .choice_border{
		display: inline-block;
		margin-top: 1px;
		margin-right: 5px;
		width: 12px;
		height: 12px;
		border: 1px solid #666;
	}
	.hide_mods .bg{
		display: inline-block;
		margin-top: 1px;
		margin-right: 5px;
		width: 14px;
		height: 14px;
		vertical-align: middle;
		background-image: url(./../../../assets/mine2.png);
		background-repeat: no-repeat;
		background-position: -128px -25px;
	}

	.hide_mods .checked{
		background-image: url(./../../../assets/mine2.png);
		background-repeat: no-repeat;
		background-position: -70px top;
	}
</style>


<template>
	<div 
		class="setup_contents_wrap"
		>
		<div 
			class="setup_contents_top">
			<div 
				class="setup_contents_top_left">
				<div 
					class="setup_contents_top_left_title">
					<span>已订阅频道</span>
				</div>
				<div 
					class="setup_contents_top_left_name">
					<span>我的关注</span>
				</div>
				<div 
					class="setup_contents_top_left_contents_wrap">
					<div
						class="setup_contents_top_left_contents"
						v-for="list in guanzhu">
						<div class="icon"></div>
						<div class="text">
							<span>
								{{ list.name }}
							</span>
						</div>
					</div>
					<div class="div_clear"></div>
				</div>	
			</div>
			<div 
				class="setup_contents_top_right">
				<setupright></setupright>
			</div>
			<div class="div_clear"></div>
		</div>
		<div 
			class="setup_contents_bottom">
			<span></span>
			<a 
				class="hide_mods"
				@click="choiceNow===1 ? choiceNow=0 : choiceNow=1">
				<span 
					class="choice_border"
					:class="choiceNow===1 ? 'checked' : '' "></span>
				<span>情景推荐</span>
			</a>
			<a
				class="hide_mods">
				<span class="bg"></span>
				<span>隐藏频道</span>
			</a>
			<div class="div_clear"></div>
		</div>
	</div>
</template>


<script>
	import setupright from './setupright'

	export default{
		props: ['setupNow'],
		data(){
			return {
				guanzhu: [
					{ name: '推荐' },
					{ name: '导航' }
				],
				choiceNow: '0'
			}
		},
		components: {
			setupright
		}
	}
</script>


