<style>
	.input_component1{
		position: absolute;
		top: 0;
		left: 0;
	}
	.input_component1 .bg_btn{
		float: left;
	    display: block;
		width: 100px;
	    height: 36px;
	    z-index: 0;
	    vertical-align: top;
	}
	.input_component1 .bg_btn>input{
		width: 100px;
	    height: 36px;
	    color: #fff;
	    font-size: 15px;
	    letter-spacing: 1px;
	    background-color: #3385ff;
	    border: 1px solid #2d78f4;
	    outline: medium;
	    -webkit-appearance: none;
	    -webkit-border-radius: 0;
	    cursor: pointer;
	}
	
	.bg_wr{
		float: left;
	   	display: block;
	   	position: relative;
	  	margin-right: 0;
	  	width: 539px;
	 	height: 34px;
		border: 1px solid #b6b6b6;
		border-color: #7b7b7b #b6b6b6 #b6b6b6 #7b7b7b;
		background-color: #fff;
		vertical-align: top;
		border-right-width: 0;
		border-color: #b8b8b8 transparent #ccc #b8b8b8;
		overflow: hidden;
	}
	.bg_wr>span{
		z-index: 1;
		display: block;
		position: absolute;
		right: 11px;
		top: 50%;
		margin-top: -8px;
		height: 16px;
		width: 18px;
		background: #fff;
		background-image: url(./../../../assets/camera.png);
		background-repeat: no-repeat;
		cursor: pointer;
	}

	.bg_wr>span:hover{
		background-position: 0 -20px;
	}

	.bg_wr>input{
		width: 500px;
		height: 22px;
		font: 16px/18px arial;
		line-height: 22px;
		margin: 6px 0 0 7px;
		padding: 0;
		background: transparent;
		border: 0;
		outline: 0;
		-webkit-appearance: none;
	}
	.input_component1 .bg_btn{
		float: left;
		display: block;
		width: 100px;
		height: 36px;
		z-index: 0;
		vertical-align: top;
	}
	.input_component1 .bg_btn>input{
		width: 100px;
		height: 36px;
		color: #fff;
		font-size: 15px;
		letter-spacing: 1px;
		background-color: #3385ff;
		border: 1px solid #2d78f4;
		outline: medium;
		-webkit-appearance: none;
		-webkit-border-radius: 0;
		cursor: pointer;
	}



	/* 鼠标滑动按钮样式 */
	.bg_btn:hover{
		height: 35px;
		border-bottom: 1px solid #2771d9;
	}
	.bg_btn:hover>input{
		background-color: #317ef3;
		border-bottom: 1px solid #2868c8;
		box-shadow: 1px 1px 1px #ccc;
	}


	/* 选用背景皮肤时，百度按钮的样式 */
	.input_component1 .baidubtn_backgroundimg{
		display: block;
		color: #000!important;
		background-color: rgb(177,177,177)!important;
		border: 1px solid rgba(0,0,0,0.2)!important;
		background-image: url('./../../../assets/baidu_btn.png');
		background-repeat: no-repeat;
		background-position: -206px 0;
		box-shadow: 1px 1px 3px rgba(0,0,0,0.2);
		-webkit-box-shadow: 1px 1px 3px rgba(0,0,0,0.2);
	}
</style>


<template>
	<div 
		class="input_component1">
		<span 
			class="bg_wr">
			<span 
				@click="btnClick"></span>
			<input v-model="searchKeyword">
		</span>
		<span 
			class="bg_btn">
			<input 
				type="submit" 
				:class="{baidubtn_backgroundimg : changebtnsomestyle}"
				value="百度一下">
		</span>
		<div 
			class="div_clear"></div>
	</div>
</template>


<script>
	export default{
		props: ['changebtnsomestyle'],
		data(){
			return{
				inputValue: '',
				searchKeyword:''
			}
		},
		methods:{
			btnClick:function(){
				this.$emit('searchModeChange','image')
			}
		}
	}
</script>


