<style>
	.danghang_contents2>div{
		margin: 8px 0 4px;
		width: 100%;
	}
	.danghang_contents2>div>span{
		font-size: 14px;
		text-align: left;
		font-weight: bold;
		color: #000;
	}
	.danghang_contents2>ul>li{
		float: left;
		display: inline-block;
		position: relative;
		margin-right: 15px;
		margin-top: 15px;
		cursor: pointer;
	}
	.danghang_contents2>ul>li>a{
		display: block;
		position: relative;
		height: 58px;
		width: 126px;
		line-height: 58px;
		background-color: #fcfcfc;
	}
	.danghang_contents2>ul>li>a>img{
		position: absolute;
	    left: 5px;
	    top: -2px;
	}

	/* 鼠标滑动 */
	.danghang_contents2>ul>li:hover>a{
		margin: -1px;
		border: 1px solid rgba(0,0,0,.05);
	}
</style>


<template>
	<div class="danghang_contents2">
		<div>
			<span>
				{{ title }}
			</span>
		</div>
		<ul>
			<li
				v-for="list in webLists"
				:title="list.webName">
				<a>
					<img
						:src="list.webImgUrl">
				</a>
			</li>
			<div class="div_clear"></div>
		</ul>
	</div>
</template>


<script>
	export default{
		data(){
			return {
				title: '资讯信息',
				webLists: [
					{
						webName: '腾讯网',
						webImgUrl: require('@/assets/tengxunwang.jpg')
					},
					{
						webName: '新浪网',
						webImgUrl: require('@/assets/xinlang.jpg')
					},
					{
						webName: '新浪微博',
						webImgUrl: require('@/assets/xinlangweibo.jpg')
					},
					{
						webName: '搜狐网',
						webImgUrl: require('@/assets/souhu.jpg')
					},
					{
						webName: '网易',
						webImgUrl: require('@/assets/wangyi.jpg')
					},
					{
						webName: '光明网',
						webImgUrl: require('@/assets/guangming.jpg')
					}
				]
			}
		}
	}
</script>


