<style>
	.topnav_right_wrap{
		float: right;
	}
	.topnav_right_wrap>li{
		float: left;
	}
	.topnav_right_wrap>li>a{
		margin-left: 20px;
		line-height: 32px;
		text-decoration: underline;
		font-size: 13px;
		color: #555;
		font-weight: bold;
		cursor: pointer;
	}
	.topnav_right_wrap .more_products{
		margin-left: 10px;
		margin-right: 0px;
		width: 95px;
		text-align: center;
		background-color: #38f;
		cursor: default;
	}
	.topnav_right_wrap .more_products>a{
		margin-left: 0px;
		width: 60px;
		height: 23px;
		color: #fff;
		text-decoration: none;
		cursor: default;
	}
	.topnav_right_wrap .people_name>a,
	.topnav_right_wrap .set_up>a{
		font-weight: normal;
	}
	.people_name,
	.set_up{
		position: relative;
	}
	.topdropmenu{
		display: none;
	}
	

	/* 鼠标滑动事件 */
	.people_name:hover .topdropmenu{
		display: block;
	}
	.set_up:hover .topdropmenu{
		display: block;
	}



	.right_more_products_wrap{
		display: block;
		position: absolute;
		right: 0;
		top: 0;
		width: 95px;
		height: 100%;
		border-left: 1px solid #e7e7e7;
		background-color: #f9f9f9;
	}

	.right_more_products_title{
	    padding-bottom: 10px;
	    margin: 0 auto;
	    width: 66px;
	    height: 30px;
	    line-height: 34px;
	    font-size: 13px;
	    text-align: center;
		color: #333;
	    border-bottom: #f0f0f0 1px solid;
	    cursor: default;
	}

	.right_more_products_app>a{
		display: block;
	    margin: 0 auto;
	    width: 66px;
	    height: 76px;
	    font-size: 12px;
	    text-align: center;
	    line-height: 14px;
	    border-bottom: #f0f0f0 1px solid;
	    text-decoration: none;
	    overflow: hidden;
	    text-shadow: none;
	    cursor: pointer;
	}

	/* 鼠标滑过 */
	.right_more_products_app>a:hover>span{
		text-decoration: underline;
	}

	.right_more_products_app>a>img{
		display: block;
		width: auto;
		height: 36px;
		margin: 10px auto 5px;
		cursor: pointer;
	}
	.right_more_products_app>a>span{
		display: block;
		color: #666!important;
		text-decoration: none;
	}

	.right_more_products_app_more{
		margin-top: 15px;
		height: 30px;
	}
	.right_more_products_app_more>a{
		display: block;
	    margin: 0 auto;
	    width: 66px;
	    height: 15px;
	    font-size: 12px;
	    text-align: center;
	    line-height: 14px;
	    color: #666;
	    text-decoration: none;
	    overflow: hidden;
	    text-shadow: none;
	    cursor: pointer;
	}

	/* 鼠标滑过 */
	.right_more_products_app_more>a:hover{
		text-decoration: underline;
	}
</style>


<template>
	<ul class="topnav_right_wrap">
		<li v-for="list in topnav_right">
			<a>
				{{ list.topnavweb }}
			</a>
		</li>
		<li class="people_name">
			<a>{{ username }}</a>
			<topdropmenu 
				class="topdropmenu"
				:message="dropmenulists_username"></topdropmenu>
		</li>
		<li class="set_up">
			<a>设置</a>
			<topsetupdropmenu 
				class="topdropmenu"
				:message="dropmenulists_setup"
				@clicktopdropmenu="clickTopdropMenuList"></topsetupdropmenu>
		</li>
		<transition name="dropshow">
			<setupsubcontents 
				v-show="clicktopdropmenulist"
				@closesetupsubcontentmenu="closeSetupSubContents"></setupsubcontents>
		</transition>
		<li 
			class="more_products"
			@mouseenter="showMore=true"
			@mouseleave="showMore=false">
			<a>更多产品</a>
			<div 
				class="right_more_products_wrap"
				v-show="showMore">
				<div class="right_more_products_title">
					<span>更多产品</span>
				</div>
				<div class="right_more_products_app">
					<a v-for="list in pro_app">
						<img :src="list.productsImgUrl">
						<span>
							{{ list.productsName }}
						</span>
					</a>
					<div class="right_more_products_app_more">
						<a>全部产品>></a>
					</div>
				</div>
			</div>
		</li>
	</ul>
</template>


<script>
	import topdropmenu from './topdropmenu.vue'
	import topsetupdropmenu from './topsetupdropmenu.vue'
	import setupsubcontents from './setupsubcontents.vue'

	export default{
		props: ['userclickoutsetupside'],
		data(){
			return {
				topnav_right: [
					{
						topnavweb: '新闻'
					},
					{
						topnavweb: 'hao123'
					},
					{
						topnavweb: '地图'
					},
					{
						topnavweb: '视频'
					},
					{
						topnavweb: '贴吧'
					},
					{
						topnavweb: '学术'
					}
				],
				username: '庹19960222',
				showMore: false,
				pro_app: [
					{
						productsImgUrl: require('@/assets/bdbri.png'),
						productsName: '糯米'
					},
					{
						productsImgUrl: require('@/assets/music.png'),
						productsName: '音乐'
					},
					{
						productsImgUrl: require('@/assets/image.png'),
						productsName: '图片'
					},
					{
						productsImgUrl: require('@/assets/zhidao.png'),
						productsName: '知道'
					},
					{
						productsImgUrl: require('@/assets/wenku.png'),
						productsName: '文库'
					},
					{
						productsImgUrl: require('@/assets/bang.png'),
						productsName: '风云榜'
					},
					{
						productsImgUrl: require('@/assets/tuiguang.png'),
						productsName: '百度推广'
					}
				],
				dropmenulists_username: [
					{ submenu: '个人中心' },
					{ submenu: '账号设置' },
					{ submenu: '意见反馈' },
					{ submenu: '退出' }
				],
				dropmenulists_setup: [
					{ submenu: '搜索设置' },
					{ submenu: '高级搜索' },
					{ submenu: '关闭预测' },
					{ submenu: '搜索历史' }
				],
				clicktopdropmenulist: this.userclickoutsetupside
			}
		},
		watch: {
			// 设置
	      	userclickoutsetupside: function(val){
	        	this.clicktopdropmenulist = val;//②监听外部对props属性showgetmessagesubcontents的变更，并同步到组件内的data属性showgetmessagesubcontents中
	        	console.log('向里');
	      	},
      		clicktopdropmenulist: function(val){
        		this.$emit('delivernewuserclick',val);//③组件内对showgetmessagesubcontents变更后向外部发送事件通知
        		console.log('向外');
      		}
		},
		methods: {
			clickTopdropMenuList: function(val){
				this.clicktopdropmenulist = val; // true 显示下拉菜单
			},
			closeSetupSubContents: function(val){
				this.clicktopdropmenulist = val; // false 关闭按钮
			},
		},
		components: {
			topdropmenu,
			topsetupdropmenu,
			setupsubcontents
		}
	}
</script>


