<style>
	/* main内容部分2 */
	.main_contents_left{
		float: left;
		margin-left: 30px;
	}
	.contents_left_title{
		width: 100%;
		height: 38px;
	}
	.contents_left_title .line_left{
		float: left;
		display: inline-block;
		height: 0;
		width: 172px;
		border-bottom: 1px solid rgba(226,226,226,1);
		margin-top: 31px;
	}
	.contents_left_title .title_text_wrap{
		float: left;
		display: inline-block;
		width: 173px;
		padding-top: 21px;
		margin: 0 5px;
	}
	.contents_left_title .title_text_wrap div.baidu_icon{
		display: inline-block;
		height: 14px;
		width: 12px;
		background-image: url(./../../../../assets/news.png);
		background-repeat: no-repeat;
		background-position: -45px -1px;
	}
	.contents_left_title .title_text_wrap span.title_text{
		display: inline-block;
		font-size: 13px;
		color: rgba(0,0,0,.4);
	}
	.contents_left_title .line_right{
		float: left;
		display: inline-block;
		height: 0;
		width: 172px;
		border-bottom: 1px solid rgba(226,226,226,1);
		margin-top: 31px;
	}
</style>


<template>
	<div>
		<div class="main_contents_left">
			<div class="contents_left_title">
				<div 
					class="line_left"
					:style="{borderBottom: '1px solid rgba(226,226,226,'+ changebackgroundopacity +')'}"></div>
				<div class="title_text_wrap">
					<div class="baidu_icon"></div>
					<span class="title_text">
						以下信息根据您的兴趣推荐
					</span>
				</div>
				<div 
					class="line_right"
					:style="{borderBottom: '1px solid rgba(226,226,226,'+ changebackgroundopacity +')'}"></div>
				<div class="div_clear"></div>
			</div>
			<newscontentsitem :changebackgroundopacity="changebackgroundopacity"></newscontentsitem>
		</div>
		<hotspotcontentsitem></hotspotcontentsitem>
		<div class="div_clear"></div>
	</div>
</template>


<script>
	import newscontentsitem from './newscontentsitem.vue'
	import hotspotcontentsitem from './hotspotcontentsitem.vue'

	export default{
		props: ['changebackgroundopacity'],
		data(){
			return{
			}
		},
		methods: {
		},
		components: {
			newscontentsitem,
			hotspotcontentsitem
		}
	}
</script>


