<style>
	.changestylesubcontents_wrap{
		z-index: 2000;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		background-color: #FFF;
		box-shadow: 0 0 2px 2px rgba(0,0,0,.07);
		-webkit-box-shadow: 0 0 2px 2px rgba(0,0,0,.07);
	}
	.changestylesubcontents_top_wrap{
		width: 100%;
		height: 46px;
		border-bottom: 1px solid #e3e3e3;
	}
	.changestylesubcontents_top{
		margin: 0 auto;
		width: 900px;
	}
	.changestylesubcontents_topnav{
		float: left;
		height: 46px;
	}
	.changestylesubcontents_topnav>li{
		float: left;
		padding: 0 8px;
		line-height: 46px;
		transition: 0.4s;
		cursor: pointer;
	}
	.changestylesubcontents_topnav>li>span{
		font-size: 13px;
		text-decoration: none;
		color: #666!important;
	}
	.changestylesubcontents_topnav .topnav_line{
		cursor: default;
	}

	
	/* 鼠标滑动样式 */
	.changestylesubcontents_topnav .topnav_li_lists:hover{
		background-color: #389cff;
	}
	.changestylesubcontents_topnav .topnav_li_lists:hover .topnav_li_lists_text{
		color: #fff!important;
	}

	/* 点击添加样式 */
	.changestylesubcontents_topnav .topnav_li_lists_click{
		background-color: #389cff;
	}
	.changestylesubcontents_topnav .topnav_li_lists_click .topnav_li_lists_text{
		color: #fff!important;
	}


	.changestylesubcontents_toolbar{
		float: right;
		height: 46px;
	}
	.backgroundtransparency,
	.dontuseskin_wrap,
	.retract_wrap{
		float: left;
		line-height: 46px;
		cursor: default;
	}
	.backgroundtransparency{
		user-select: none;  /* 不被选中 */
	}
	.backgroundtransparency span,
	.dontuseskin_wrap span,
	.retract_wrap span{
		font-size: 13px;
		text-decoration: none;
		color: #666!important;
	}
	.backgroundtransparency_text,
	.backgroundtransparency_bar_wrap{
		float: left;
	}
	/* 自定义 */
	.backgroundtransparency_bar_wrap{
		position: relative;
		margin-top: 22px;
		margin-left: 3px;
	}
	.backgroundtransparency_bar{
		display: block;
		position: relative;
		width: 80px;
	    height: 4px;
	    line-height: 16px;
	    border-top: 1px solid #e1e1e1;
	    background: #f2f2f2;
	    font-size: 0;
	    cursor: pointer;
	}
	.backgroundtransparency_btn{
		display: inline-block;
		position: absolute;
		top: -6px;
		right: 0px;
		width: 12px;
		height: 12px;
		border-top: 1px solid #d4d4d4;
	    border-left: 1px solid #d4d4d4;
	    border-right: 1px solid #e1e1e1;
	    border-bottom: 1px solid #e1e1e1;
	    background-color: #fff;
	    cursor: pointer;
	}
	.backgroundtransparency_btn>span.backgroundtransparency_counts{
	    position: absolute;
	    top: -16px;
	    left: -8px;
	    display: block;
	    width: 28px;
	    height: 14px;
	    line-height: 14px;
	    text-align: center;
	    font-size: 11px;
	    color: #666;
	    font-family: normal;
	    font-style: normal;
	    cursor: default;
	}
	.dontuseskin_wrap{
		margin-left: 25px;
		cursor: pointer;
	}
	.dontuseskin_wrap .dontuseskin_icon{
		display: inline-block;
		width: 14px;
		height: 13px;
		font-size: 13px;
		color: #666;
		background-image: url(./../../assets/changestylesubmenu_icon.png);
		background-position: -24px -175px;
		background-repeat: no-repeat;
	}
	.dontuseskin_wrap .dontuseskin_text{
		display: inline-block;
		font-size: 13px;
		color: #666;
	}

	/* 鼠标滑动不使用皮肤字体改变颜色 */
	.dontuseskin_wrap:hover .dontuseskin_text{
		color: #0079f5!important;
	}
	.dontuseskin_wrap:hover .dontuseskin_icon{
		background-position: -24px -191px;
	}

	.retract_wrap{
		margin-left: 25px;
		cursor: pointer;
	}
	.retract_wrap .retract_icon{
		display: inline-block;
		width: 11px;
		height: 15px;
		background-image: url(./../../assets/changestylesubmenu_icon.png);
		background-position: -44px -173px;
		background-repeat: no-repeat;
	}
	.retract_wrap .retract_text{
		font-size: 13px;
		color: #0079f5;
		font-weight: normal;
	}
	

	.changestylesubcontents_picmain_wrap{
		margin: 20px auto 0;
		width: 900px;
		height: 242px
	}
	.changestylesubcontents_picdetail{
		float: right;
		width: 266px;
	}
	.picdetail_main_wrap{
		position: relative;
		display: inline-block;
		width: 264px;
		height: 180px;
		background-image: url(./../../assets/showskin.png);
		background-repeat: no-repeat;
		background-position: -275px 0;
	}
	.picdetail_main_wrap>img{
		display: none;
		z-index: -1;
		position: absolute;
		top: 15px;
		left: 2px;
		width: 260px;
		height: 163px;
	}
	
	/* 鼠标滑动皮肤改变背景和图片 */
	.use_skin{
		background-position: 0 0;
	}
	.use_skin>img{
		display: block;
	}

	.picdetail_bottomtext_wrap{
		margin-top: 5px;
		width: 100%;
		text-align: center;
	}
	.picdetail_bottomtext_text{
		color: #666;
		font-size: 12px;
	}
</style>


<template>
	<div 
		class="changestylesubcontents_wrap"
		@click.stop>
		<div class="changestylesubcontents_top_wrap">
			<div class="changestylesubcontents_top">
				<ul class="changestylesubcontents_topnav">
					<li 
						class="topnav_lists topnav_li_lists"
						:class="{'topnav_li_lists_click' : clicked === -1}"
						@click="clickBtns(-1)">
						<span class="topnav_lists_text topnav_li_lists_text">热门</span>
					</li>
					<li
						v-for="(list,index) in topnavlists"
						class="topnav_lists topnav_li_lists"
						:class="{'topnav_li_lists_click' : clicked === index}"
						@click="clickBtns(index)">
						<span class="topnav_lists_text topnav_li_lists_text">
							{{ list.stylelist }}
						</span>
					</li>
					<li class="topnav_line">
						<span class="topnav_line_text">|</span>
					</li>
					<li 
						class="topnav_custom topnav_li_lists"
						:class="{'topnav_li_lists_click' : clicked === 7}"
						@click="clickBtns(7)">
						<span class="topnav_custom_text topnav_li_lists_text">自定义</span>
					</li>
					<li 
						class="topnav_recently topnav_li_lists"
						:class="{'topnav_li_lists_click' : clicked === 8}"
						@click="clickBtns(8)">
						<span class="topnav_recently_text topnav_li_lists_text">最近使用</span>
					</li>
				</ul>
				<div class="changestylesubcontents_toolbar">
					<div 
						class="backgroundtransparency"
						v-show="useskin">
						<span class="backgroundtransparency_text">背景透明度</span>
						<div 
							class="backgroundtransparency_bar_wrap"
							@mousemove="moveBackgroundTransparencyBtn($event)"
							@mouseleave="leaveBackgroundTransparencyBtn($event)">
							<div 
								class="backgroundtransparency_bar"
								@click="clickBackgroundTransparencyBtn($event)">
							</div>
							<div 
								class="backgroundtransparency_btn"
								@mousedown="selectedBackgroundTransparencyBtn($event)"
								@mouseup="leaveBackgroundTransparencyBtn($event)"
								:style="{left : distance+'px'} ">
								<span class="backgroundtransparency_counts">
									{{ degree_backgroundopacity }}%
								</span>
							</div>
						</div>
						<div class="div_clear"></div>
					</div>
					<div 
						class="dontuseskin_wrap"
						@click="notUseSkin"
						v-show="useskin">
						<span class="dontuseskin_icon"></span>
						<span class="dontuseskin_text">不使用皮肤</span>
					</div>
					<div 
						class="retract_wrap"
						@click="retractChangeStyle()">
						<span class="retract_icon"></span>
						<span class="retract_text">收起</span>
					</div>
				</div>
				<div class="div_clear"></div>
			</div>
		</div>
		<div class="changestylesubcontents_picmain_wrap">
			<div v-show="this.clicked === -1">
				<skinpicframe
					@showtoolbar="selectedBackgroundSkin" 
					@showpicdetail="getPicSrc"
					@seepicdetail="handleSeePicDetail"
					@changebackgroundimg="backgroundImgSrc"
					@changesomestyle="changeStyle"
					@checkclickskin="clickNormalSkin"
					:confirmselectedrecentusedskin="saveselectedsrc"
					:skinpic="hotskinpic1"
					:restractbtn="restracticon"></skinpicframe>
				<div class="changestylesubcontents_picdetail">
					<div
						:class="seeskin ? 'use_skin' : ''" 
						class="picdetail_main_wrap">
						<img :src="detailsrc">
					</div>
					<div class="picdetail_bottomtext_wrap">
						<span class="picdetail_bottomtext_text">背景皮肤效果预览</span>
					</div>
				</div>
			</div>
			<div
				v-show="this.clicked === 8">
				<recentused
					:confirmselectedskin="saveselectedsrc"
					:recentusedsrc="recentusedskin"
					:restractbtn="restracticon"
					@showpicdetail="getPicSrc"
					@getbackgroundsrc="usedbackgroundImgSrc"
					@changetoolbar="selectedBackgroundSkin"
					@checkclickrecentused="clickRecentUsed"
					@changebackgroundimg="backgroundImgSrc"></recentused>
			</div>
		</div>
	</div>
</template>


<script>
	import skinpicframe from './skinpicframe.vue'
	import recentused from './recentused.vue'

	export default{
		data(){
			return {
				clicked: -1,
				topnavlists: [
					{ stylelist: '游戏' },
					{ stylelist: '卡通' },
					{ stylelist: '女神降临' },
					{ stylelist: '明星' },
					{ stylelist: '风景' },
					{ stylelist: '简约' },
					{ stylelist: '小清新' }
				],
				degree_backgroundopacity: '100',
				showchangestyle: false,
				useskin: false,
				seeskin: false,
				detailsrc: '',
				emptysrc: '',
				changeword: false,
				cancelsomestyle: false,

				pos: {},// 获取一个空的数组
		        startX: 0, 
		        locked: false,
		        distance: 68, //当前位置
		        endDistance: 0, //上次操作结束位置
		        dragWidth: 0, //进度条宽度
		        btnWidth: 0,// 按钮宽度
		        startNum: 0, // 头的数值
		        endNum: 100, // 尾的数值
		        changebackgroundopacity: 1, // 背景颜色透明度改变
		        hotskinpic1: [
					{
						className: 'position_1 big_img',
						skinImgUrl: require('@/assets/background_hotimg01.jpg'),
						pictext: '摄影:最强NBA'
					},
					{
						className: 'position_2',
						skinImgUrl: require('@/assets/background_hotimg02.jpg'),
						shadowClassName: 'skin_img_shadow',
						pictext: '摄影:最强NBA'
					},
					{
						className: 'position_3',
						skinImgUrl: require('@/assets/background_hotimg03.jpg'),
						shadowClassName: 'skin_img_shadow',
						pictext: '摄影:剑灵'
					},
					{
						className: 'position_4',
						skinImgUrl: require('@/assets/background_hotimg04.jpg'),
						shadowClassName: 'skin_img_shadow',
						pictext: '摄影:白敬亭'
					},
					{
						className: 'position_5',
						skinImgUrl: require('@/assets/background_hotimg05.jpg'),
						shadowClassName: 'skin_img_shadow',
						pictext: '摄影:一品芝麻狐'
					},
					{
						className: 'position_6',
						skinImgUrl: require('@/assets/background_hotimg06.jpg'),
						shadowClassName: 'skin_img_shadow',
						pictext: '摄影:一品芝麻狐'
					},
					{
						className: 'position_7',
						skinImgUrl: require('@/assets/background_hotimg07.jpg'),
						shadowClassName: 'skin_img_shadow',
						pictext: '摄影:一品芝麻狐'
					},
					{
						className: 'position_8',
						skinImgUrl: require('@/assets/background_hotimg08.jpg'),
						shadowClassName: 'skin_img_shadow',
						pictext: '摄影:一品芝麻狐'
					},
					{
						className: 'position_9 big_img',
						skinImgUrl: require('@/assets/background_hotimg09.jpg'),
						shadowClassName: 'skin_img_shadow',
						pictext: '摄影:一品芝麻狐'
					},
					{
						className: 'position_10',
						skinImgUrl: require('@/assets/background_hotimg10.jpg'),
						shadowClassName: 'skin_img_shadow',
						pictext: '摄影:一品芝麻狐'
					},
					{
						className: 'position_11',
						skinImgUrl: require('@/assets/background_hotimg11.jpg'),
						shadowClassName: '',
						pictext: ''
					},
					{
						className: 'position_12',
						skinImgUrl: require('@/assets/background_hotimg12.jpg'),
						shadowClassName: '',
						pictext: ''
					}
				],

				// 是否显示最近使用绿色按钮或正常皮肤绿色按钮
				clicknormalskin: false,
				clickrecentusedskin: false,
				restracticon: false,

				// 空数组  存储皮肤图片的src
				recentusedskin: [],
				// 传过来的src
				getnewimgsrc: '',

				// 声明一个变量用于传递已经选中的图片的src
				saveselectedsrc: ''
			}
		},
		methods: {
			clickBtns (index){
				this.clicked = index;
			},
			retractChangeStyle (){
				this.restractbtns=false;
				this.$emit('clickrestractbtns',this.restractbtns);
			},
			selectedBackgroundSkin (useskin){
				this.useskin = useskin;
			},
			getPicSrc (detailsrc){
				this.detailsrc = detailsrc;
			},
			handleSeePicDetail (seeskin){
				this.seeskin = seeskin;
			},
			// 背景图片src的传递
			backgroundImgSrc (src){
				this.$emit('changebackgroundimg',src);
				this.saveselectedsrc=src;
				this.getnewimgsrc=src;// 把新的src路径赋值给getnewimgsrc
				// 先在数组里加入数据
				this.recentusedskin.unshift({ usedskinsrc: src });
			},
			// 最近使用点击图片不进行判断
			usedbackgroundImgSrc (src){
				this.$emit('changebackgroundimg',src);
				this.saveselectedsrc=src;
			},
			// 不使用皮肤的方法
			notUseSkin (){
				this.useskin=false;
				this.seeskin=false;
				this.restracticon=true;
				//this.selectedskin=false;
				//this.selectedrecentusedskin=false;
				this.$emit('changebackgroundimg',this.emptysrc);
				this.$emit('changetopwordcolor',this.changeword);
				this.$emit('changesomestyle',this.cancelsomestyle);
				this.detailsrc='';
			},
			changeStyle (change){
				this.$emit('changesomestyle',change);
			},

			// 点击最近使用的皮肤，正常皮肤绿色选中按钮消失
			clickRecentUsed: function(recentusedskin){
				this.clickrecentusedskin = recentusedskin;
			},
			// 点击正常皮肤，最近使用皮肤绿色选中按钮消失
			clickNormalSkin: function(normalskin){
				this.clicknormalskin = normalskin;
			},

			// 保存已经选中图片的src
			/*saveSelectSrc: function(src){
				this.saveselectedsrc = src;
			},*/


			// 改变背景透明度
			// 当鼠标移到进度条时触发
			moveBackgroundTransparencyBtn (e){
				// locked为false时跳出
				if (!this.locked) return;
	          	var pos = this.mousePos(e);
	          	var moveX = pos.x - this.startX;// 距离上次停止时的位置移动的距离
	          	var length = this.dragWidth - this.btnWidth;// 进度条长度 - 按钮大小
	          	// 如果按钮的left(this.distance)大于长度length后停止继续移动
          		// 判断按钮移动的区间
          		if ( this.distance <= 0 && moveX < 0 ){
            		this.distance = 0;
            	}else if ( this.distance >= length && moveX > 0){
            		this.distance = length;
            	}else{
            		this.distance = this.endDistance + moveX;
	          	}
	            this.countNum(this.distance);
	            this.$emit('changebackgroundopacity',this.changebackgroundopacity);
			},
			// 当鼠标移到进度条并且按下鼠标左键时触发
			selectedBackgroundTransparencyBtn (e){
	          	this.mousePos(e);
	         	this.startX = this.pos.x; // this.startX为开始时距离左侧的坐标，this.pos.x为按钮距离左侧的坐标(变动)
	          	this.locked = true; // 允许执行moveBackgroundTransparencyBtn方法
	          	this.endDistance = this.distance;// 把当前位置distance赋值给结束位置endDistance
			},
			// 当鼠标移出进度条或松开鼠标时触发
			leaveBackgroundTransparencyBtn (e){
				// 不再执行
				this.locked = false;
			},
			// 当鼠标点击进度条时触发
			clickBackgroundTransparencyBtn (e){
				var x = e.offsetX,
	          	moveX = x - this.distance;
	          	var length = this.dragWidth - this.btnWidth;// 进度条长度 - 按钮大小
	          	this.distance = moveX + this.distance - this.btnWidth/2;
	          	if(this.distance > length){
	          		this.distance = length;
	          	}
	          	if(this.distance < 0){
	          		this.distance = 0;
	          	}
	          	this.countNum(this.distance);
	          	// 改变透明度
          		this.$emit('changebackgroundopacity',this.changebackgroundopacity);
			},
			// 获取鼠标距离文档左边缘的位置
			mousePos (e){
				var pos = {};
	          	pos.x = e.pageX;
	          	this.pos = pos;
	         	return pos;
			},
			// 计算出移动后的长度并显示
			countNum (num){
				var len = this.endNum - this.startNum;// 计算进度条数值的大小 100
          		//var scale = Math.ceil(this.dragWidth / len);// 计算进度条数值占进度条长度的比例 80/100 (0.8)
          		var length = this.dragWidth - this.btnWidth;// 进度条长度 - 按钮大小
          		this.degree_backgroundopacity = Math.round(num / length * 100);// 计算移动后的长度
          		if(this.degree_backgroundopacity<0){
          			this.degree_backgroundopacity=0;
          		}
          		// 改变透明度
          		this.changebackgroundopacity=Math.round(this.degree_backgroundopacity)/100;
			}
		},
		mounted: function(){
			//this.length = this.dragWidth; // 80 - 12
			this.btnWidth = $('.backgroundtransparency_btn').width();// 计算按钮大小 12
			this.dragWidth = $('.backgroundtransparency_bar').width();// 计算进度条的长度 80
		},
		components: {
			skinpicframe,
			recentused
		},
		computed: {
			
		},
		watch: {
			getnewimgsrc: function(val){ 
				// val为string类型
				var _this=this;
				// 进行数组数据判断是否相同
				this.recentusedskin.forEach(function(value,index){
					// 当数组为空时，不进行判断
					if(index==0){
						return;
					}else{
						if(val===value.usedskinsrc){
							// 获取相同数据的索引值index
							// 根据索引值删除之前的数据
							_this.recentusedskin.splice(index,1);
						}
					}
					//console.log(_this.recentusedskin)
				})
			}
		}
	}
</script>


