<style>
	p{
		margin: 0;
		padding: 0;	
	}
	.recentskinpicframe_wrap{
		position: relative;
		float: left;
		width: 100%;
		height: 242px;
	}
	/* 框的样式 */
	.recentskin_empty{
		float: left;
		position: relative;
		margin: 1.5px;
	    width: 97px;
	    height: 59px;
	    background-color: #f5f5f5;
	    cursor: default;
	}
	/* 加入图片的框 */
	.recentskin_img_item{
		float: left;
		position: relative;
		margin: 1.5px;
	    width: 97px;
	    height: 59px;
	    background-color: #f5f5f5;
	    cursor: pointer;
	} 

	.recentskin_img_item>img{
		width: 97px;
		height: 59px;
	}

	.recentskinpic_icon{
		position: absolute;
		bottom: 4px;
		right: 4px;
		display: block;
		width: 18px;
		height: 18px;
		font-size: 13px;
		background-image: url(./../../assets/skin_icon.png);
		background-repeat: no-repeat;
		background-position: -180px 0;

	}
</style>


<template>
	<ul class="recentskinpicframe_wrap">
		<!-- 加入图片的框 -->
		<li
			class="recentskin_img_item"
			v-for="(list,index) in recentusedsrc"
			@click="selectedBackgroundSkin(index,list.usedskinsrc)">
			<img :src="list.usedskinsrc">
			<div 
				class="recentskinpic_icon"
				v-show="list.usedskinsrc === confirmselectedskin"></div>
		</li>
		<!-- 空的框 -->
		<li
			class="recentskin_empty"
			v-for="list in recentmessage"></li>
		<div class="div_clear"></div>
	</ul>
</template>


<script>
	export default{
		props: ['confirmselectedskin','recentusedsrc'],
		data(){
			return {
				useskin: true,
				selectedskin: ''
			}
		},
		computed: {
			recentmessage (){
				return 27-this.recentusedsrc.length;
			}
		},
		methods: {
			selectedBackgroundSkin: function(index,src){
				this.selectedskin = index;
				this.$emit('showpicdetail',src);
				this.$emit('getbackgroundsrc',src);
				this.$emit('changetoolbar',this.useskin);
			}
		},
		watch: {
		}
	}
</script>


