<style>
	.getmessagesubcontents_wrap{
		position: absolute;
		top: 32px;
		left: 50%;
		margin-left: -141px;
		cursor: default;
	}
	.getmessagesubcontents_wrap .menu_arrow{
		position: absolute;
		top: -10px;
		left: 50%;
		margin-left: -5px;
		display: inline-block;
		width: 0;
		height: 0;
		border: 5px dashed transparent;
		border-bottom: 5px solid #e3e3e3;
	}
	.getmessagesubcontents_wrap .menu_arrow em{
		position: absolute;
	    top: -4px;
	    left: -5px;
	    display: block;
	    width: 0;
	    height: 0;
	    color: #666;
	    border-top: 5px dashed transparent;
	    border-left: 5px dashed transparent;
	    border-right: 5px dashed transparent;
	    border-bottom: 5px solid #fff;
	}
	.getmessagesubcontents_main{
		height: 139px;
		border: 1px solid #e3e3e3;
		background-color: #fff;
		box-shadow: 0 2px 2px rgba(0,0,0,0.07);
		-webkit-box-shadow: 0 2px 2px rgba(0,0,0,0.07);
	}
	.getmessagesubcontents_message{
		margin: 0 20px;
		height: 88px;
		line-height: 88px;
		border-bottom: 1px solid rgba(177,177,177,.20);
		text-align: center;
	}
	.getmessagesubcontents_message .showgetmessage{
		font-size: 12px;
		color: #333;
		font-weight: normal;
	}
	.getmessagesubcontents_btns{
		width: 230px;
		margin-left: 50px;
		padding-top: 10px;
		padding-bottom: 12px;
		height: 28px;
	}
	.getmessagesubcontents_btns .clearmessage_btn{
		float: left;
		display: inline-block;
		margin-right: 15px;
		width: 82px;
		height: 28px;
		line-height: 26px;
		text-align: center;
		background-color: #bcbcbc;
		color: #FFF;
		cursor: default;
	}
	.getmessagesubcontents_btns .clearmessage_btn .clearmessage_icon{
		padding-left: 13px;
		width: 12px;
		height: 9px;
		font-size: 12px;
		background-image: url(./../../assets/getmessage_icon.png);
		background-repeat: no-repeat;
		background-position: 0 5px;
	}
	.getmessagesubcontents_btns .clearmessage_btn .clearmessage_text{
		color: #FFF;
		font-size: 12px;
	}
	.getmessagesubcontents_btns .setupmessage_btn{
		float: left;
		display: inline-block;
		width: 82px;
		height: 28px;
		line-height: 26px;
		text-align: center;
		background-color: #389cff;
		color: #FFF;
		cursor: pointer;
	}
	.getmessagesubcontents_btns .setupmessage_btn .setupmessage_icon{
		padding-left: 15px;
		width: 12px;
		height: 10px;
		font-size: 12px;
		background-image: url(./../../assets/getmessage_icon.png);
		background-repeat: no-repeat;
		background-position: -15px 4px;
	}
	.getmessagesubcontents_btns .setupmessage_btn .setupmessage_message{
		color: #FFF;
		font-size: 12px;
	}
</style>


<template>
	<div 
		class="getmessagesubcontents_wrap"
		@click.stop>
		<span class="menu_arrow">
			<em></em>
		</span>
		<div class="getmessagesubcontents_main">
			<div class="getmessagesubcontents_message">
				<span class="showgetmessage">{{ showgetmessage }}</span>
			</div>
			<div class="getmessagesubcontents_btns">
				<div class="clearmessage_btn">
					<span class="clearmessage_icon"></span>
					<span class="clearmessage_text">清空消息</span>
				</div>
				<div class="setupmessage_btn">
					<a class="setupmessage_icon"></a>
					<span class="setupmessage_message">消息设置</span>
				</div>
			</div>
		</div>
	</div>
</template>


<script>
	export default{
		data(){
			return {
				showgetmessage: '您目前没有新消息'
			}
		}
	}
</script>


